<template>
  <div>
    <!--  -->
    <el-breadcrumb separator-class="el-icon-arrow-right">
      <el-breadcrumb-item :to="{ path: '/home' }"></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
    </el-breadcrumb>

    <el-card>
      <el-form
        ref="addFormRules"
        label-width="80px"
        :rules="addFormRules"
        :model="addInfo"
      >
        <el-form-item label="" prop="stuEnrollmentNumber">
          <el-input v-model="addInfo.stuEnrollmentNumber"></el-input>
        </el-form-item>
        <el-form-item label="" prop="stuName">
          <el-input v-model="addInfo.stuName"></el-input>
        </el-form-item>
        <el-form-item label="" prop="punishmentByPerson">
          <el-input v-model="addInfo.punishmentByPerson"></el-input>
        </el-form-item>
        <el-form-item label="" prop="punishmentName">
          <el-input v-model="addInfo.punishmentName"></el-input>
        </el-form-item>
        <el-form-item label="" prop="punishmentType">
          <el-input v-model="addInfo.punishmentType"></el-input>
        </el-form-item>
        <el-form-item label="" prop="punishmentDesc">
          <el-input v-model="addInfo.punishmentDesc"></el-input>
        </el-form-item>
        <el-form-item label="" prop="punishmentTime">
          <el-input v-model="addInfo.punishmentTime"></el-input>
        </el-form-item>
        <el-form-item label="" prop="punishmentTimeLong">
          <el-input v-model="addInfo.punishmentTimeLong"></el-input>
        </el-form-item>
        <el-form-item label="" prop="punishmentFinish">
          <el-input v-model="addInfo.punishmentFinish"></el-input>
        </el-form-item>
      </el-form>
      <el-row>
        <el-button
          type="primary"
          @click="addStudentPunish()"
          v-loading.fullscreen.lock="fullscreenLoading"
          ><i class="el-icon-circle-plus"></i></el-button
        >
        <el-button type="warning" @click="resetInfo('addFormRules')"
          ><i class="el-icon-refresh-right"></i></el-button
        >
      </el-row>
    </el-card>
  </div>
</template>

<script>
export default {
  name: "app",
  data() {
    return {
      fullscreenLoading: false,
      addInfo: {
        stuEnrollmentNumber: "",
        stuName: "",
        punishmentByPerson: "",
        punishmentName: "",
        punishmentType: "",
        punishmentDesc: "",
        punishmentTime: "",
        punishmentTimeLong: "",
        punishmentFinish: "",
      },
      //
      addFormRules: {
        //
        stuEnrollmentNumber: [
          { required: true, message: "", trigger: "blur" },
          {
            min: 20,
            max: 20,
            message: " 20 ",
            trigger: "blur",
          },
        ],
        // 
        stuName: [
          { required: true, message: "", trigger: "blur" },
          {
            min: 2,
            max: 8,
            message: "28",
            trigger: "blur",
          },
        ],
        punishmentTime: [
          { required: true, message: "", trigger: "blur" },
          {
            min: 2,
            message: "2",
            trigger: "blur",
          },
        ],
        punishmentName: [
          { required: true, message: "", trigger: "blur" },
          {
            min: 2,
            message: "2",
            trigger: "blur",
          },
        ],
        punishmentType: [
          { required: true, message: "", trigger: "blur" },
          {
            min: 2,
            message: "2",
            trigger: "blur",
          },
        ],
        punishmentDesc: [
          { required: true, message: "", trigger: "blur" },
          {
            min: 2,
            message: "2",
            trigger: "blur",
          },
        ],
        punishmentByPerson: [
          { required: true, message: "", trigger: "blur" },
          {
            min: 2,
            message: "2",
            trigger: "blur",
          },
        ],
        punishmentTimeLong: [
          {
            required: true,
            message: "",
            trigger: "blur",
          },
          {
            min: 2,
            message: "2",
            trigger: "blur",
          },
        ],
        punishmentFinish: [
          {
            required: true,
            message: "",
            trigger: "blur",
          },
          {
            min: 2,
            message: "2",
            trigger: "blur",
          },
        ],
      },
    };
  },
  methods: {
    // 
    resetInfo(addFormRules) {
      this.$refs[addFormRules].resetFields();
    },
    addStudentPunish() {
      this.$refs.addFormRules.validate(async (valid) => {
        if (!valid) return;
        const confirmResult = await this.$confirm(
          "?",
          "",
          {
            confirmButtonText: "",
            cancelButtonText: "",
            type: "warning",
          }
        ).catch((err) => err);
        if (confirmResult !== "confirm") {
          return this.$message.info("");
        }
        this.fullscreenLoading = true;
        const { data: res } = await this.$http.post(
          "/graduate-project/addStuPunish",
          this.addInfo
        );
        if (res.status !== 200) {
          this.$http.error("");
        }
        this.$http.get(
          "/graduate-project/switchPunishToTrue?stuEnrollmentNumber=" +
            this.addInfo.stuEnrollmentNumber
        );
        this.$message.success(res.message);
        this.addInfo = {};
        this.fullscreenLoading = false;
      });
    },
  },
};
</script>

<style scoped>
.el-row {
  float: right;
  margin-bottom: 15px;
}
</style>
