<template>
  <div>
    <!--  -->
    <el-breadcrumb separator-class="el-icon-arrow-right">
      <el-breadcrumb-item :to="{ path: '/home' }"></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
    </el-breadcrumb>

    <el-card>
      <el-table border stripe :data="errReportData" style="width: 100%">
        <el-table-column label="#" type="index"></el-table-column>
        <el-table-column prop="stuEnrollmentNumber" label="">
        </el-table-column>
        <el-table-column prop="stuName" label=""> </el-table-column>
        <el-table-column fixed="right" label="" width="150">
          <template slot-scope="scope">
            <el-link
              type="primary"
              :underline="true"
              icon="el-icon-chat-dot-round"
              @click="handleViewInfo(scope.row)"
              v-loading.fullscreen.lock="fullscreenLoading"
              ></el-link
            >
            <el-link
              type="primary"
              :underline="true"
              icon="el-icon-edit"
              @click="handleDeal(scope.row)"
              v-loading.fullscreen.lock="fullscreenLoading"
              ></el-link
            >
          </template>
        </el-table-column>
      </el-table>

      <!--  -->
      <el-pagination
        :page-sizes="[3, 5, 10]"
        layout="total, sizes, prev, pager, next, jumper"
        :total="total"
        :current-page="queryPageInfo.pagenum"
        :page-size="queryPageInfo.pagesize"
        @size-change="handleSizeChange"
        @current-change="handleCurrentChange"
      >
      </el-pagination>
    </el-card>

    <!--  -->
    <el-dialog
      @close="editDialogClosed"
      title=""
      :visible.sync="viewReportVisible"
      width="60%"
    >
      <el-table border stripe :data="errReportTableData" style="width: 100%" v-loading="loading">
        <el-table-column label="#" type="index"></el-table-column>
        <el-table-column prop="questionModule" label="">
        </el-table-column>
        <el-table-column prop="questionDesc" label="">
        </el-table-column>
        <el-table-column prop="questionStatus" label=""> </el-table-column>
      </el-table>
    </el-dialog>

    <!--  -->
    <el-dialog
      @close="editDialogClosed"
      title=""
      :visible.sync="editDialogVisible"
      fullscreen
    >
      <!--  -->
      <el-form
        :model="editForm"
        :rules="editFormRules"
        ref="editFormRef"
        label-width="100px"
      >
        <el-form-item label="" prop="stuEnrollmentNumber">
          <el-input v-model="editForm.stuEnrollmentNumber"></el-input>
        </el-form-item>
        <el-form-item label="" prop="stuIdentifyNum">
          <el-input v-model="editForm.stuIdentifyNum"></el-input>
        </el-form-item>
        <el-form-item label="" prop="stuName">
          <el-input v-model="editForm.stuName"></el-input>
        </el-form-item>
        <el-form-item label="" prop="stuPassName">
          <el-input v-model="editForm.stuPassName"></el-input>
        </el-form-item>
        <el-form-item label="" prop="stuGender">
          <el-select v-model="editForm.stuGender" placeholder="">
            <el-option label="" value="man"></el-option>
            <el-option label="" value="women"></el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="" prop="stuAge">
          <el-input v-model="editForm.stuAge"></el-input>
        </el-form-item>
        <el-form-item label="" prop="stuAddress">
          <el-input v-model="editForm.stuAddress"></el-input>
        </el-form-item>
        <el-form-item label="" prop="stuPoliticsStatus">
          <el-select
            v-model="editForm.stuPoliticsStatus"
            placeholder=""
          >
            <el-option label="" value="socialman"></el-option>
            <el-option label="" value="leaguemember"></el-option>
            <el-option label="" value="chinamember"></el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="" prop="stuNativePlace">
          <el-input v-model="editForm.stuNativePlace"></el-input>
        </el-form-item>
        <el-form-item label="" prop="stuNation">
          <el-input v-model="editForm.stuNation"></el-input>
        </el-form-item>
        <el-form-item label="" prop="stuPhoneNumber">
          <el-input v-model="editForm.stuPhoneNumber"></el-input>
        </el-form-item>
        <el-form-item label="" prop="stuHomeAddress">
          <el-input v-model="editForm.stuHomeAddress"></el-input>
        </el-form-item>
        <el-form-item label="" prop="stuPostCode">
          <el-input v-model="editForm.stuPostCode"></el-input>
        </el-form-item>
        <el-form-item label="" prop="stuHealth">
          <el-select v-model="editForm.stuHealth" placeholder="">
            <el-option label="" value="health"></el-option>
            <el-option label="" value="better"></el-option>
            <el-option label="" value="nothealth"></el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="" prop="stuEducationExperience">
          <el-input v-model="editForm.stuEducationExperience"></el-input>
        </el-form-item>
        <el-form-item label="" prop="stuHonor">
          <el-input v-model="editForm.stuHonor"></el-input>
        </el-form-item>
        <el-form-item label="" prop="stuPunishment">
          <el-input v-model="editForm.stuPunishment"></el-input>
        </el-form-item>
      </el-form>
      <!--  -->
      <span slot="footer" class="dialog-footer">
        <el-button @click="editDialogVisible = false"> </el-button>
        <el-button type="primary" @click="editInfo()" v-loading.fullscreen.lock="fullscreenLoading"> </el-button>
      </span>
    </el-dialog>
  </div>
</template>

<script>
export default {
  name: "app",
  data() {
    return {
      isFullscreen: true,
      loading: false,
      fullscreenLoading: false,
      editDialogVisible: false,
      viewReportVisible: false,
      total: 0,
      // 
      queryPageInfo: {
        pagenum: 1,
        pagesize: 5,
      },
      errReportData: [],
      errReportTableData: [],
      // 
      editFormRules: {
        //
        stuEnrollmentNumber: [
          { required: true, message: "", trigger: "blur" },
          {
            min: 20,
            max: 20,
            message: " 20 ",
            trigger: "blur",
          },
        ],
        // 
        stuIdentifyNum: [
          { required: true, message: "", trigger: "blur" },
          {
            min: 18,
            max: 18,
            message: " 18 ",
            trigger: "blur",
          },
        ],
        // 
        stuName: [
          { required: true, message: "", trigger: "blur" },
          {
            min: 2,
            max: 8,
            message: "28",
            trigger: "blur",
          },
        ],
        // 
        stuPassName: [
          { required: false },
          {
            min: 2,
            max: 8,
            message: "28",
            trigger: "blur",
          },
        ],
        // 
        stuGender: [{ required: true, message: "", trigger: "blur" }],
        // 
        stuAddress: [
          { required: true, message: "", trigger: "blur" },
          {
            min: 4,
            max: 40,
            message: "440",
            trigger: "blur",
          },
        ],
        // 
        stuPoliticsStatus: [
          { required: true, message: "", trigger: "blur" },
        ],
        // 
        stuNativePlace: [
          { required: true, message: "", trigger: "blur" },
          {
            min: 3,
            max: 12,
            message: "312",
            trigger: "blur",
          },
        ],
        // 
        stuNation: [
          { required: true, message: "", trigger: "blur" },
          {
            min: 2,
            max: 12,
            message: "212",
            trigger: "blur",
          },
        ],
        // 
        stuPhoneNumber: [
          { required: true, message: "", trigger: "blur" },
          {
            min: 11,
            max: 11,
            message: "11",
            trigger: "blur",
          },
        ],
        // 
        stuHomeAddress: [
          { required: true, message: "", trigger: "blur" },
          {
            min: 4,
            max: 40,
            message: "440",
            trigger: "blur",
          },
        ],
        // 
        stuPostCode: [
          { required: true, message: "", trigger: "blur" },
          {
            min: 6,
            max: 6,
            message: "6",
            trigger: "blur",
          },
        ],
        // 
        stuHealth: [
          { required: true, message: "", trigger: "blur" },
        ],
        // 
        stuEducationExperience: [
          { required: true, message: "", trigger: "blur" },
          {
            min: 10,
            message: "10",
            trigger: "blur",
          },
        ],
        // 
        stuName: [
          { required: true, message: "", trigger: "blur" },
          {
            min: 2,
            max: 8,
            message: "28",
            trigger: "blur",
          },
        ],
      },
      // 
      editForm: {},
    };
  },
  mounted() {
    this.getStudentsHaveErr();
  },
  methods: {
    // 
    handleDeal(row) {
      this.fullscreenLoading = true
      this.editDialogVisible = true;
      if (row.stuGender === "") {
        row.stuGender = Number(0);
      } else if (row.stuGender === "") {
        row.stuGender = Number(1);
      }
      if (row.stuPoliticsStatus === "") {
        row.stuPoliticsStatus = Number(0);
      } else if (row.stuPoliticsStatus === "") {
        row.stuPoliticsStatus = Number(1);
      } else if (row.stuPoliticsStatus === "") {
        row.stuPoliticsStatus = Number(2);
      }
      if (row.stuHealth === "") {
        row.stuHealth = Number(0);
      } else if (row.stuHealth === "") {
        row.stuHealth = Number(1);
      } else if (row.stuHealth === "") {
        row.stuHealth = Number(-1);
      }
      if (row.stuHonor === "") {
        row.stuHonor = "";
      } else {
        row.stuHonor = Number(1);
      }
      if (row.stuPunishment === "") {
        row.stuPunishment = "";
      } else {
        row.stuPunishment = Number(1);
      }
      this.editForm = row;
      this.fullscreenLoading = false
      // console.log(this.editForm);
    },
    // 
    editInfo() {
      if (this.editForm.stuGender === "man") {
        this.editForm.stuGender = Number(0);
      } else if (this.editForm.stuGender === "women") {
        this.editForm.stuGender = Number(1);
      }
      if (this.editForm.stuPoliticsStatus === "socialman") {
        this.editForm.stuPoliticsStatus = Number(0);
      } else if (this.editForm.stuPoliticsStatus === "leaguemember") {
        this.editForm.stuPoliticsStatus = Number(1);
      } else if (this.editForm.stuPoliticsStatus === "chinamember") {
        this.editForm.stuPoliticsStatus = Number(2);
      }
      if (this.editForm.stuHealth === "health") {
        this.editForm.stuHealth = Number(0);
      } else if (this.editForm.stuHealth === "better") {
        this.editForm.stuHealth = Number(1);
      } else if (this.editForm.stuHealth === "nohealth") {
        this.editForm.stuHealth = Number(-1);
      }
      if (this.editForm.stuHonor === "") {
        this.editForm.stuHonor = "";
      } else {
        this.editForm.stuHonor = Number(1);
      }
      if (this.editForm.stuPunishment === "") {
        this.editForm.stuPunishment = "";
      } else {
        this.editForm.stuPunishment = Number(1);
      }
      // console.log(this.editForm);
      this.$refs.editFormRef.validate(async (valid) => {
        if (!valid) return;
        const confirmResult = await this.$confirm(
          "?",
          "",
          {
            confirmButtonText: "",
            cancelButtonText: "",
            type: "warning",
          }
        ).catch((err) => err);
        if (confirmResult !== "confirm") {
          return this.$message.info("");
        }
        this.fullscreenLoading = true
        const { data: res } = await this.$http.post(
          "/graduate-project/editStuInfo",
          this.editForm
        );
        if (res.status !== 200) {
          this.$message.error("");
        }
        // console.log(res.result);
        await this.$http.get(
          "/graduate-project/switchErrStatusToFalse?stuEnrollmentNumber=" +
            this.editForm.stuEnrollmentNumber
        );
        await this.$http.get(
          "/graduate-project/switchquestionStatusToTrue?stuEnrollmentNumber=" +
            this.editForm.stuEnrollmentNumber
        );
        this.$message.success(res.message);
        this.editDialogVisible = false;
        this.getStudentsHaveErr();
        this.fullscreenLoading = false
      });
    },
    async handleViewInfo(row) {
      this.fullscreenLoading = true
      this.viewReportVisible = true;
      const { data: res } = await this.$http.get(
        "/graduate-project/getErrReportsInfo?stuEnrollmentNumber=" +
          row.stuEnrollmentNumber
      );
      if (res.status !== 200) {
        this.$message.warning(res.message);
      } else if (res.status === 200) {
        this.errReportTableData = res.result;
        this.errReportTableData.forEach((value, key) => {
          // console.log(value,key);
          if (value.questionStatus === 0) {
            value.questionStatus = "";
          } else {
            value.questionStatus = "";
          }
        });
        this.fullscreenLoading = false
      }
    },
    //
    async getStudentsHaveErr() {
      this.loading= true
      const { data: res } = await this.$http.get(
        "/graduate-project/getStuInfoHaveErr",
        {
          params: this.queryPageInfo,
        }
      );
      if (res.status === 200) {
        // console.log(res.result);
        this.errReportData = res.result;
        this.errReportData.forEach((value, key) => {
          // console.log(value);
          if (value.stuGender === 0) {
            value.stuGender = "";
          } else {
            value.stuGender = "";
          }
          if (value.stuHealth === 0) {
            value.stuHealth = "";
          } else if (value.stuHealth === 1) {
            value.stuHealth = "";
          } else if (value.stuHealth === -1) {
            value.stuHealth = "";
          }
          if (value.stuPoliticsStatus === 0) {
            value.stuPoliticsStatus = "";
          } else if (value.stuPoliticsStatus === 1) {
            value.stuPoliticsStatus = "";
          } else if (value.stuPoliticsStatus === 2) {
            value.stuPoliticsStatus = "";
          }
        });
        // this.$message.success(res.message);
        this.total = res.total;
        this.loading= false
      }
    },
    // 
    handleSizeChange(newSize) {
      this.queryPageInfo.pagesize = newSize;
      this.getStudentsHaveErr();
    },
    handleCurrentChange(newPage) {
      this.queryPageInfo.pagenum = newPage;
      this.getStudentsHaveErr();
    },
    // 
    editDialogClosed() {
      this.editDialogVisible = false;
    },
  },
};
</script>

<style scoped>
</style>
