<template>
  <el-container class="home-container">
    <!--  -->
    <el-header>
      <div>
        <img src="https://z3.ax1x.com/2021/04/25/cxl5kj.png" alt="" />
        <span></span>
      </div>
      <el-button
        type="danger"
        @click="logout"
        plain
        icon="el-icon-switch-button"
        ></el-button
      >
    </el-header>
    <!--  -->
    <el-container style="height: 100vh">
      <!--  -->
      <el-aside :width="isCollapse ? '64px' : '200px'" style="height: 100vh">
        <div class="toggle-button" @click="toggleCollapse">|||</div>

        <!--  -->
        <el-menu
          :collapse="isCollapse"
          :router="true"
          :default-active="activePath"
          :collapse-transition="false"
        >
          <el-menu-item index="/welcome">
            <i class="el-icon-s-platform"></i>
            <span slot="title"></span>
          </el-menu-item>
          <el-menu-item index="/stuInfo">
            <i class="el-icon-user-solid"></i>
            <span slot="title" @getStuInfoList="getStuInfoList()"
              ></span
            >
          </el-menu-item>
          <el-menu-item index="/addStuInfo">
            <i class="el-icon-circle-plus-outline"></i>
            <span slot="title"></span>
          </el-menu-item>
          <el-menu-item index="/stuPublish">
            <i class="el-icon-notebook-1"></i>
            <span slot="title" @getPulishStudentsInfo="getPulishStudentsInfo()"
              ></span
            >
          </el-menu-item>
          <el-menu-item index="/stuHonor">
            <i class="el-icon-copy-document"></i>
            <span slot="title" @getPulishStudentsInfo="getHonorStudentsInfo()"
              ></span
            >
          </el-menu-item>
          <el-menu-item index="/addStuHonor">
            <i class="el-icon-circle-plus"></i>
            <span slot="title" @getPulishStudentsInfo="addHonorStudentsInfo()"
              ></span
            >
          </el-menu-item>
          <el-menu-item index="/addStuPunish">
            <i class="el-icon-set-up"></i>
            <span slot="title" @getPulishStudentsInfo="addPunishStudentsInfo()"
              ></span
            >
          </el-menu-item>
          <el-menu-item index="/stuErrReportsDeal">
            <i class="el-icon-s-comment"></i>
            <span slot="title" @getPulishStudentsInfo="addPunishStudentsInfo()"
              ></span
            >
          </el-menu-item>
          <el-menu-item index="/stuAchievementOpration"
            ><i class="el-icon-reading"></i>
            <span slot="title" @getPulishStudentsInfo="addPunishStudentsPonit()"
              ></span
            >
          </el-menu-item>
          <!-- <el-submenu index="/stuAchievement">
            <template slot="title"
              ><i class="el-icon-s-data"></i></template
            >   
              <el-menu-item index="/stuAchievement/view"><i class="el-icon-reading"></i></el-menu-item>
              <el-menu-item index="/stuAchievement/print"><i class="el-icon-download"></i></el-menu-item>
            </el-submenu>
          </el-submenu> -->
        </el-menu>
      </el-aside>

      <!--  -->
      <el-main style="height: 100vh">
        <!--  -->
        <router-view></router-view>
      </el-main>
    </el-container>
  </el-container>
</template>

<script>
export default {
  data() {
    return {
      // 
      isCollapse: false,
      // 
      activePath: "/welcome",
    };
  },
  created() {},
  methods: {
    async logout() {
      // 
      const confirmResult = await this.$confirm(
        ", ?",
        "",
        {
          confirmButtonText: "",
          cancelButtonText: "",
          type: "warning",
        }
      ).catch((err) => err);
      if (confirmResult !== "confirm") {
        return this.$message.info("");
      }
      //token
      window.sessionStorage.clear();
      this.$message.success("");
      this.$router.push("/login");
    },
    // 
    toggleCollapse() {
      this.isCollapse = !this.isCollapse;
    },
    getStuInfoList() {
      this.getStuInfoList();
    },
    getPulishStudentsInfo() {
      this.getPulishStudentsInfo();
    },
  },
};
</script>

<style lang="less" scoped>
.el-header {
  background: linear-gradient(to right, #cc95c0, #dbd4b4, #7aa1d2);
  display: flex;
  justify-content: space-between;
  padding-left: 10px;
  align-items: center;
  color: #fff;
  font-size: 20px;
  > div {
    display: flex;
    align-items: center;
    span {
      margin-left: 15px;
    }
    img {
      border-radius: 50%;
      width: 40px;
      height: 40px;
    }
  }
}
.home-container {
  height: 100%;
}
.el-aside {
  background: linear-gradient(to right, #cc95c0, #dbd4b4, #7aa1d2);
  background-color: #dbd4b4;
  height: 91vh;
  .el-menu {
    background: linear-gradient(to right, #cc95c0, #dbd4b4, #7aa1d2);
    text-color: "#fff";
    active-text-color: "#409EFF";
    border-right: none;
  }
}
.el-main {
  background: linear-gradient(to right, #cc95c0, #dbd4b4, #7aa1d2);
}
.iconfont {
  margin-right: 10px;
}
.toggle-button {
  background: linear-gradient(to right, #cc95c0, #dbd4b4, #7aa1d2);
  font-size: 10px;
  line-height: 24px;
  color: #fff;
  text-align: center;
  letter-spacing: 0.2em;
  cursor: pointer;
}
</style>
