<template>
  <div class="login_container">
    <div class="login_box">
      <!--  -->
      <div class="avator_box">
        <img src="https://z3.ax1x.com/2021/04/25/cxl5kj.png" alt="" />
      </div>
      <!--  -->
      <el-form
        label-width="0px"
        class="login_form"
        ref="loginFormRef"
        :rules="loginFormRules"
        :model="loginForm"
      >
        <!--  -->
        <el-form-item prop="loginName" style="margin-top: 120px">
          <el-input
            placeholder=""
            prefix-icon="iconfont icon-user"
            v-model="loginForm.loginName"
          ></el-input>
        </el-form-item>
        <!--  -->
        <el-form-item prop="loginPassword">
          <el-input
            :show-password="isShowPas"
            placeholder=""
            type="password"
            prefix-icon="iconfont icon-3702mima"
            v-model="loginForm.loginPassword"
          ></el-input>
        </el-form-item>

        <!--  -->
        <el-form-item style="width: 180px" prop="identifyCode">
          <el-input
            type="text"
            prefix-icon="el-icon-lollipop"
            v-model="loginForm.identifyCode"
            placeholder=""
            style="flex: left"
            class="identify-input"
          ></el-input>
          <div class="login-code" @click="refreshCode">
            <!---->
            <identify :identifyCode="identifyCode"></identify>
          </div>
        </el-form-item>

        <!--  -->
        <el-form-item class="btns">
          <el-button
            type="primary"
            @click="login"
            v-loading.fullscreen.lock="fullscreenLoading"
            ></el-button
          >
          <el-button type="warning" @click="resetInfo('loginFormRef')"
            ></el-button
          >
          <!-- <el-button type="danger" @click="modifyPasswordVisible = true"
            ></el-button
          > -->
        </el-form-item>
      </el-form>
    </div>

    <el-dialog
      title=""
      :visible.sync="modifyPasswordVisible"
      :fullscreen="isFull"
      :destroy-on-close="isDitroy"
      :close-on-click-modal="isModal"
      :close-on-press-escape="isCloseEsc"
    >
      <el-form label-width="8vh" class="demo-ruleForm">
        <el-form-item label="">
          <el-input></el-input>
        </el-form-item>
        <el-form-item label="">
          <el-input></el-input>
        </el-form-item>
        <el-form-item label="">
          <el-input></el-input>
        </el-form-item>
        <el-form-item label="">
          <el-input></el-input>
        </el-form-item>
        <el-form-item label="">
          <el-input></el-input>
        </el-form-item>
        <el-form-item label="">
          <el-input></el-input>
        </el-form-item>
      </el-form>
      <span slot="footer" class="dialog-footer">
        <el-button type="primary"> </el-button>
        <el-button @click="modifyPasswordVisible = false"> </el-button>
      </span>
    </el-dialog>
  </div>
</template>

<script>
import Identify from "./identify";
export default {
  components: { Identify },
  name: "Login",
  data() {
    return {
      isShowPas: true,
      isDitroy: true,
      isCloseEsc: false,
      isModal: false,
      isFull: true,
      modifyPasswordVisible: false,
      identifyCodes: "1234567890",
      identifyCode: "",
      fullscreenLoading: false,
      // 
      loginForm: {
        loginName: "",
        loginPassword: "",
        identifyCode: "", //text
      },
      //
      loginFormRules: {
        //
        loginName: [
          { required: true, message: "", trigger: "blur" },
          {
            min: 2,
            max: 20,
            message: " 2  20 ",
            trigger: "blur",
          },
        ],
        // 
        loginPassword: [
          { required: true, message: "", trigger: "blur" },
          {
            min: 4,
            max: 15,
            message: " 4  15 ",
            trigger: "blur",
          },
        ],
        // 
        identifyCode: [
          { required: true, message: "", trigger: "blur" },
          {
            min: 4,
            max: 4,
            message: "4",
            trigger: "blur",
          },
        ],
      },
    };
  },
  mounted() {
    this.refreshCode();
  },
  created() {
    this.refreshCode();
  },
  methods: {
    //
    randomNum(min, max) {
      return Math.floor(Math.random() * (max - min) + min);
    },

    refreshCode() {
      this.identifyCode = "";
      this.makeCode(this.identifyCodes, 4);
    },
    makeCode(o, l) {
      for (let i = 0; i < l; i++) {
        this.identifyCode += this.identifyCodes[
          this.randomNum(0, this.identifyCodes.length)
        ];
      }
      // console.log(this.identifyCode);
    },
    // 
    resetInfo(loginFormRef) {
      this.$refs[loginFormRef].resetFields();
    },
    // 
    login() {
      this.$refs.loginFormRef.validate(async (valid) => {
        if (!valid) return;
        if (this.identifyCode !== this.loginForm.identifyCode) {
          this.$notify({
            message: "",
            type: "error",
          });
          this.loginForm.identifyCode = "";
          this.refreshCode();
          return;
        }
        this.fullscreenLoading = true;
        const { data: res } = await this.$http.post("login", this.loginForm);
        // console.log(res);
        if (res.err_code != 200) {
          this.$notify({
            message: "",
            type: "error",
          });
          this.fullscreenLoading = false;
        }
        if (res.role.roleId == 1) {
          this.fullscreenLoading = false;
          this.$notify({
            title: "",
            message: "",
            type: "success",
          });
          //1.tokensessionStorage
          //1.1API
          //1.2tokentokensessionStorage
          window.sessionStorage.setItem("token", res.token);
          window.localStorage.setItem("admin_name", res.role.loginName);
          //2./home
          this.$router.push("/home");
        } else if (res.role.roleId == 0) {
          this.fullscreenLoading = false;
          // this.$message.success(res.role.stuName + "");
          this.$notify({
            title: "" + res.role.stuName,
            message: "",
            type: "success",
          });
          window.sessionStorage.setItem("token", res.token);
          //
          window.localStorage.setItem(
            "stu_enrollmentNumber",
            res.role.stuEnrollmentNumber
          );
          window.localStorage.setItem("stu_name", res.role.stuName);
          window.localStorage.setItem("stu_passKey", res.role.stuPassword);
          window.localStorage.setItem("avartar_src", res.role.stuAvatar);
          this.$router.push("/stuhome");
        }
      });
    },
  },
};
</script>

<style lang="less" scoped>
.login_container {
  background-image: url(https://z3.ax1x.com/2021/04/28/gihrZj.jpg);
  // background-repeat: no-repeat;
  background-size: 100%;
  height: 100vh;
}

.login_box {
  width: 350px;
  height: 400px;
  background: linear-gradient(to right, #108dc7, #ef8e38);
  border-radius: 10px;
  position: absolute;
  left: 0;
  right: 0;
  bottom: 0;
  top: 0;
  margin: auto;
}

.avator_box {
  height: 130px;
  width: 130px;
  border: 1px solid #eee;
  border-radius: 50%;
  padding: 10px;
  box-shadow: 0 0 10px #ddd;
  position: absolute;
  left: 50%;
  transform: translate(-50%, -50%);
  background-color: #fff;
}

img {
  width: 100%;
  height: 100%;
  border-radius: 50%;
  background-color: #eee;
}

.login_form {
  position: absolute;
  bottom: 0;
  width: 100%;
  padding: 0 20px;
  box-sizing: border-box;
}

.btns {
  display: flex;
  justify-content: space-between;
  // margin-left: 12px;
}
// .identify-input {
//   width: 170px;
//   height: 31px;
//   border: 1px solid #eee;
//   background-color: #eee;
// }
.login-code {
  cursor: pointer;
  margin-left: 185px;
  border-radius: 40%;
  margin-top: -36px;
}
</style>
