<template>
  <div>
    <!--  -->
    <el-breadcrumb separator-class="el-icon-arrow-right">
      <el-breadcrumb-item :to="{ path: '/home' }"></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
    </el-breadcrumb>

    <!--  -->
    <el-card> 
      <el-row :gutter="20">
        <el-col :span="8">
          <!--  -->
          <el-input
            :clearable="true"
            v-model="stuName"
            placeholder=""
          >
          </el-input>
        </el-col>
        <el-col :span="8">
          <el-button type="primary" @click="searchPublishInfo()" icon="el-icon-search"
            ></el-button
          >
          <el-button type="warning" @click="reset()" icon="el-icon-refresh-right"
            ></el-button
          >
        </el-col>
      </el-row>

      <!--  -->
      <el-table border stripe :data="dataSource" v-loading="loading">
        <el-table-column label="#" type="index"></el-table-column>
        <el-table-column label="" prop="stuEnrollmentNumber" width="200px"></el-table-column>
        <el-table-column label="" prop="stuIdentifyNum" width="180px"></el-table-column>
        <el-table-column label="" prop="stuName" width="180px"></el-table-column>
        <el-table-column label="" prop="stuPassName" width="180px"></el-table-column>
        <el-table-column label="" width="180px" prop="stuGender"> </el-table-column>
        <el-table-column label="" width="180px" prop="stuAge"> </el-table-column>
        <el-table-column label="" width="180px" prop="stuAddress"> </el-table-column>
        <el-table-column label="" width="180px" prop="stuPoliticsStatus" </el-table-column>
        <el-table-column label="" width="180px" prop="stuNativePlace"> </el-table-column>
        <el-table-column label="" width="180px" prop="stuNation" </el-table-column>
        <el-table-column label="" width="180px" prop="stuPhoneNumber"> </el-table-column>
        <el-table-column label="" width="180px" prop="stuHomeAddress"> </el-table-column>
        <el-table-column label="" width="180px" prop="stuPostCode"> </el-table-column>
        <el-table-column label="" width="180px" prop="stuHealth"> </el-table-column>
        <!-- <el-table-column label="" width="180px"><el-link type="primary" icon="el-icon-s-promotion" :underline="false"></el-link></el-table-column>
        <el-table-column label="" width="180px"><el-link type="primary" icon="el-icon-s-promotion" :underline="false"></el-link></el-table-column>
        <el-table-column label="" width="180px"><el-link type="primary" icon="el-icon-s-promotion" :underline="false"></el-link></el-table-column> -->
        <el-table-column
      fixed="right"
      label=""
      width="130">
      <template slot-scope="scope">
        <el-link type="primary" :underline="false" icon="el-icon-paperclip" @click="viewHonor(scope.row)" v-loading.fullscreen.lock="fullscreenLoading"></el-link> 
      </template>
    </el-table-column>
      </el-table>

      <!--  -->
      <el-pagination
        :page-sizes ="[3,5,10]"
        layout="total, sizes, prev, pager, next, jumper"
        :total="total"
        :current-page="queryPageInfo.pagenum"
        :page-size="queryPageInfo.pagesize"
        @size-change="handleSizeChange"
        @current-change="handleCurrentChange"
      >
      </el-pagination>
    </el-card>

    <!--  -->
    <el-dialog 
      @close="viewDialogClosed"
      title=""
      :visible.sync="viewDialogVisible"
      width="60%"
      >
       <el-table
       border stripe
      :data="viewTableData"
      style="width: 100%">
      <el-table-column label="#" type="index"></el-table-column>
      <el-table-column
        prop="honorTime"
        label=""
        width="180">
      </el-table-column>
      <el-table-column
        prop="honorName"
        label=""
        width="180">
      </el-table-column>
      <el-table-column
        prop="honorType"
        label=""
        width="180">
      </el-table-column>
      <el-table-column
        prop="honorDesc"
        label="">
      </el-table-column>
    </el-table>
    </el-dialog>
  </div>
</template>

<script>
export default {
  name: "StuPublish",
  data() {
    return {
      fullscreenLoading: false,
      loading: false,
      viewTableData: [],
      viewDialogVisible: false,
      // 
      stuName: "",
      total: 0,
      // 
      queryPageInfo: {
        pagenum: 1,
        pagesize: 5,
      },
      dataSource: [],
    };
  },
  mounted() {
    this.getHonorStudentsInfo();
    this.$emit("getHonorStudentsInfo", this.getHonorStudentsInfo);
  },
  methods: {
    // 
    async viewHonor(row) {
      this.fullscreenLoading = true
    //   console.log(row.stuEnrollmentNumber);
      const { data: res } = await this.$http.get(
        "/graduate-project-punish/viewHonor?stuEnrollmentNumber=" +
          row.stuEnrollmentNumber
      );
      if (res.status !== 200) {
        this.$message.warning(res.message);
      } else if (res.status === 200) {
        // console.log(res.result);
        this.viewTableData = res.result;
        // this.$message.success(res.message);
        this.viewDialogVisible = true;
        this.fullscreenLoading = false
      }
    },
    viewDialogClosed() {
      this.viewDialogVisible = false;
    },
    reset() {
      this.loading = true
      this.stuName = ""
      this.getHonorStudentsInfo()
      this.loading = false
    },
    // 
    async searchPublishInfo() {
      this.loading = true
      const {
        data: res,
      } = await this.$http.post(
        "/graduate-project/searchStuInfoHavePublishByName",
        { stuName: this.stuName.trim() }
      );
      if (res.status !== 200) {
        this.$message.error("");
      }
      // console.log(res);
      this.dataSource = res.result;
      this.total = res.total
      this.loading = false
    },
    // 
    handleSizeChange(newSize) {
      this.loading = true
      this.queryPageInfo.pagesize = newSize;
      this.getPulishStudentsInfo();
      this.loading = false
    },
    handleCurrentChange(newPage) {
      this.loading = true
      this.queryPageInfo.pagenum = newPage;
      this.getPulishStudentsInfo();
      this.loading = false
    },
    // 
    async cancelPublish(row) {
      // console.log(row);
      const confirmResult = await this.$confirm(
        "?",
        "",
        {
          confirmButtonText: "",
          cancelButtonText: "",
          type: "warning",
        }
      ).catch((err) => err);
      if (confirmResult !== "confirm") {
        return this.$message.info("");
      }
      const { data: res } = await this.$http.get(
        "/graduate-project/cancelStuPublish?stuEnrollmentNumber=" +
          row.stuEnrollmentNumber
      );
      if (res.status !== 200) {
        this.$message.error("");
      }
      // this.$message.success(res.message);
      // 
      console.log(row);
      this.deletePunish(row)
      this.getPulishStudentsInfo();
    },
    async deletePunish(row) {
     const {data: res} = await this.$http.delete('/graduate-project-punish/deletePunish?stuEnrollmentNumber='+ row.stuEnrollmentNumber)
     if (res.status !== 200) {
        this.$message.error("");
      }
      this.$message.success(res.message)
    },
    // 
    async getHonorStudentsInfo() {
      this.loading = true
      const {
        data: res,
      } = await this.$http.get(
        "/graduate-project/getStuInfoHaveHonor?stuHonor=" + 1,
        { params: this.queryPageInfo }
      );
      if (res.status === 200) {
        // console.log(res.result);
        this.dataSource = res.result;
        this.dataSource.forEach((value, key) => {
          // console.log(value);
          if (value.stuGender === 0) {
            value.stuGender = "";
          } else {
            value.stuGender = "";
          }
          if (value.stuHealth === 0) {
            value.stuHealth = "";
          } else if (value.stuHealth === 1) {
            value.stuHealth = "";
          } else if (value.stuHealth === -1) {
            value.stuHealth = "";
          }
          if (value.stuPoliticsStatus === 0) {
            value.stuPoliticsStatus = "";
          } else if (value.stuPoliticsStatus === 1) {
            value.stuPoliticsStatus = "";
          } else if (value.stuPoliticsStatus === 2) {
            value.stuPoliticsStatus = "";
          }
        });
        // this.$message.success(res.message);
        this.total = res.total;
        this.loading = false
      }
    },
  },
};
</script>

<style lang="less" scoped>
</style>
