<template>
  <div>
    <!--  -->
    <el-breadcrumb separator-class="el-icon-arrow-right">
      <el-breadcrumb-item :to="{ path: '/home' }"></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
    </el-breadcrumb>

    <el-card>
      <el-form
        ref="addFormRules"
        label-width="80px"
        :rules="addFormRules"
        :model="addInfo"
      >
        <el-form-item label="" prop="stuEnrollmentNumber">
          <el-input v-model="addInfo.stuEnrollmentNumber"></el-input>
        </el-form-item>
        <el-form-item label="" prop="stuName">
          <el-input v-model="addInfo.stuName"></el-input>
        </el-form-item>
        <el-form-item label="" prop="honorTime">
          <el-input v-model="addInfo.honorTime"></el-input>
        </el-form-item>
        <el-form-item label="" prop="honorName">
          <el-input v-model="addInfo.honorName"></el-input>
        </el-form-item>
        <el-form-item label="" prop="honorType">
          <el-input v-model="addInfo.honorType"></el-input>
        </el-form-item>
        <el-form-item label="" prop="honorDesc">
          <el-input v-model="addInfo.honorDesc"></el-input>
        </el-form-item>
      </el-form>
      <el-row>
        <el-button
          type="primary"
          @click="addStudentHonor()"
          v-loading.fullscreen.lock="fullscreenLoading"
          ><i class="el-icon-circle-plus"></i></el-button
        >
        <el-button type="warning" @click="resetInfo('addFormRules')"
          ><i class="el-icon-refresh-right"></i></el-button
        >
      </el-row>
    </el-card>
  </div>
</template>

<script>
export default {
  name: "app",
  data() {
    return {
      fullscreenLoading: false,
      addInfo: {
        stuEnrollmentNumber: "",
        stuName: "",
        honorTime: "",
        honorName: "",
        honorType: "",
        honorDesc: "",
      },
      //
      addFormRules: {
        //
        stuEnrollmentNumber: [
          { required: true, message: "", trigger: "blur" },
          {
            min: 20,
            max: 20,
            message: " 20 ",
            trigger: "blur",
          },
        ],
        // 
        stuName: [
          { required: true, message: "", trigger: "blur" },
          {
            min: 2,
            max: 8,
            message: "28",
            trigger: "blur",
          },
        ],
        honorTime: [
          { required: true, message: "", trigger: "blur" },
          {
            min: 2,
            message: "2",
            trigger: "blur",
          },
        ],
        honorName: [
          { required: true, message: "", trigger: "blur" },
          {
            min: 2,
            message: "2",
            trigger: "blur",
          },
        ],
        honorType: [
          { required: true, message: "", trigger: "blur" },
          {
            min: 2,
            message: "2",
            trigger: "blur",
          },
        ],
        honorDesc: [
          { required: true, message: "", trigger: "blur" },
          {
            min: 2,
            message: "2",
            trigger: "blur",
          },
        ],
      },
    };
  },
  methods: {
    // 
    addStudentHonor() {
      this.$refs.addFormRules.validate(async (valid) => {
        if (!valid) return;
        const confirmResult = await this.$confirm(
          ",",
          "",
          {
            confirmButtonText: "",
            cancelButtonText: "",
            type: "warning",
          }
        ).catch((err) => err);
        if (confirmResult !== "confirm") {
          return this.$message.info("");
        }
        this.fullscreenLoading = true
        const { data: res } = await this.$http.post(
          "/graduate-project/addStuHonor",
          this.addInfo
        );
        if (res.status !== 200) {
          this.$http.error("");
        }
        this.$http.get(
          "/graduate-project/switchHornorStatus?stuEnrollmentNumber=" +
            this.addInfo.stuEnrollmentNumber
        );
        this.$message.success(res.message);
        this.addInfo = {};
        this.fullscreenLoading = false
      });
    },
    // 
    resetInfo(addFormRules) {
      this.$refs[addFormRules].resetFields();
    },
  },
};
</script>

<style scoped>
.el-row {
  float: right;
  margin-bottom: 15px;
}
</style>
