<template>
  <div>
    <!--  -->
    <el-breadcrumb separator-class="el-icon-arrow-right">
      <el-breadcrumb-item :to="{ path: '/home' }"></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
    </el-breadcrumb>

    <el-card>
      <el-row :gutter="20">
        <el-col :span="8">
          <!--  -->
          <el-input
            :clearable="true"
            v-model="stuEnrollmentNumberQuery"
            placeholder=""
          >
          </el-input>
        </el-col>
        <el-col :span="12">
          <el-button
            type="primary"
            @click="searchStudentInfo()"
            icon="el-icon-search"
            ></el-button
          >
          <el-button type="warning" icon="el-icon-refresh" @click="reset()"
            ></el-button
          >
          <el-upload
            style="margin-top: -40px; margin-left: 200px"
            :auto-upload="false"
            action="#"
            :limit="1"
            :on-change="handleUpload"
            ref="upload"
          >
            <el-button type="success" icon="el-icon-upload2" @click="reset()"
              ></el-button
            >
          </el-upload>
        </el-col>
      </el-row>

      <el-table border stripe :data="stuInfoData" style="width: 100%" v-loading="loading">
        <el-table-column label="#" type="index"></el-table-column>
        <el-table-column prop="stuEnrollmentNumber" label="">
        </el-table-column>
        <el-table-column prop="stuName" label=""> </el-table-column>
        <el-table-column fixed="right" label="" width="150">
          <template slot-scope="scope">
            <el-link
              type="primary"
              :underline="true"
              icon="el-icon-chat-dot-round"
              @click="handleViewPoint(scope.row)"
               v-loading.fullscreen.lock="fullscreenLoading"
              ></el-link
            >
          </template>
        </el-table-column>
      </el-table>

      <!--  -->
      <el-pagination
        :page-sizes="[3, 5, 10]"
        layout="total, sizes, prev, pager, next, jumper"
        :total="total"
        :current-page="queryPageInfo.pagenum"
        :page-size="queryPageInfo.pagesize"
        @size-change="handleSizeChange"
        @current-change="handleCurrentChange"
      >
      </el-pagination>
    </el-card>

    <!--  -->
    <el-dialog
    @close="handleClose()"
      title=""
      :visible.sync="voPointDialogVisible"
      :fullscreen="isFull"
    >
      <el-row>
        <span></span>
        <el-select v-model="value" clearable placeholder="">
          <el-option
            v-for="item in options"
            :key="item.value"
            :label="item.label"
            :value="item.value"
          >
          </el-option>
        </el-select>

        <!--  -->
        <el-button
          style="margin-left: 12px"
          type="primary"
          icon="el-icon-search"
          @click="getPoint()"
          ></el-button
        >

        <!--  -->
        <el-button
          type="success"
          @click="exportStudentPoint"
          icon="el-icon-download"
          ></el-button
        >
        <!-- <el-upload
          style="margin-top: 15px"
          :auto-upload="false"
          action="#"
          :limit="1"
          :on-change="handleUpload"
          ref="upload"
        >
          <el-button type="primary" icon="el-icon-upload2"
            ></el-button
          >
        </el-upload> -->
      </el-row>

      <!--  -->
      <el-table border stripe :data="dataSource" v-loading="loading">
        <el-table-column label="#" type="index"></el-table-column>
        <!-- <el-table-column label="" prop="stuEnrollmentNumber"></el-table-column>
        <el-table-column label="" prop="stuName"></el-table-column>
        <el-table-column label="" prop="stuPointRange"></el-table-column> -->
        <el-table-column label="" prop="stuSubjectName"></el-table-column>
        <el-table-column label="" prop="stuCredit"></el-table-column>
        <el-table-column label="" prop="stuGpa"></el-table-column>
        <el-table-column label="" prop="stuGrade"></el-table-column>
      </el-table>

      <!--  -->
      <span slot="footer" class="dialog-footer">
        <!-- <el-button @click="voPointDialogVisible = false"> </el-button> -->
      </span>
    </el-dialog>
  </div>
</template>

<script>
import XLSX from "../../../node_modules/xlsx/xlsx";
export default {
  name: "app",
  data() {
    return {
      fullscreenLoading: false,
      loading: false,
      isFull: true,
      // 
      pointQueryParam: "",
      // 
      voPointDialogVisible: false,
      // 
      stuEnrollmentNumberQuery: "",
      // 
      stuInfoData: [],
      // 
      total: 0,
      // 
      queryPageInfo: {
        pagenum: 1,
        pagesize: 5,
      },
      url: {
        stuInfoUrl: "/graduate-project/stuInfo",
      },
      options: [
        {
          value: "1",
          label: "",
        },
        {
          value: "2",
          label: "",
        },
        {
          value: "3",
          label: "",
        },
        {
          value: "4",
          label: "",
        },
        {
          value: "5",
          label: "",
        },
      ],
      value: "",
      // 
      dataSource: [],
      // 
      excelData: [],
    };
  },
  mounted() {
    this.getStuPointData();
  },
  methods: {
    // 
    async handleUpload(file) {
      let that = this;
      // Excel
      if (
        file.raw.type !=
          "application/vnd.openxmlformats-officedocument.spreadsheetml.sheet" &&
        file.raw.type != "application/vnd.ms-excel"
      ) {
        this.$notify.error({
          title: "",
          message: "(Excel)",
        });
        this.$refs.upload.clearFiles();
        return false;
      }
      //
      let reader = new FileReader();
      let flag =
        typeof FileReader !== "undefined" &&
        (FileReader.prototype || {}).readAsBinaryString;
      if (flag) {
        reader.readAsBinaryString(file.raw);
      } else {
        reader.readAsArrayBuffer(file.raw);
      }
      reader.onload = await function (e) {
        let data = e.target.result;
        let workBook = XLSX.read(data, { type: "binary" });
        workBook.SheetNames.forEach(function (name) {
          var XL_row_object = XLSX.utils.sheet_to_row_object_array(
            workBook.Sheets[name]
          );
          if (XL_row_object.length > 0) {
            that.excelData = JSON.parse(JSON.stringify(XL_row_object));
          }
          console.log(that.excelData);
          // json
          // that.excelData.forEach((item, index) => {
          //   let obj = {};
          //   for (let i in item) {
          //     if (i !== "stuEnrollmentNumber" && i !== "stuName") {
          //       obj[i] = item[i];
          //       delete item[i];
          //     }
          //   }
          //   item.grade = obj;
          // });
          this.loading= true
          that.$http
            .post("/graduate-project/importExcel", that.excelData)
            .then((res) => {
              if (res.data.success) {
                that.$refs.upload.clearFiles();
                that.$message.success("");
                that.getPoint();
                this.loading= false
              } else {
                that.$refs.upload.clearFiles();
                that.$message.error("");
              }
            });
        });
      };
    },
    // 
    exportStudentPoint() {
      require.ensure([], () => {
        const { export_json_to_excel } = require("../../excel/Export2Excel");
        const tHeader = ["", "", "", "", ""];
        const filterVal = [
          "stuPointRange",
          "stuSubjectName",
          "stuCredit",
          "stuGpa",
          "stuGrade",
        ];
        const list = this.dataSource;
        const data = this.formatJson(filterVal, list);
        export_json_to_excel(tHeader, data, "");
      });
    },
    formatJson(filterVal, jsonData) {
      return jsonData.map((v) => filterVal.map((j) => v[j]));
    },
    // 
    async getPoint() {
      this.loading= true
      // console.log(this.value);
      if (this.value == "") {
        this.$message.warning('')
        this.loading= false
      }
      else if (this.value == 1) {
        const { data: res } = await this.$http.get(
          "/graduate-project/getFirstPoint?stuEnrollmentNumber=" +
            this.pointQueryParam
        );
        if (res.status !== 200) {
          this.$message.error("");
        } else {
          // console.log(res.result);
          this.dataSource = res.result;
          this.loading= false
        }
      } else if (this.value == 2) {
        const { data: res } = await this.$http.get(
          "/graduate-project/getSecondPoint?stuEnrollmentNumber=" +
            this.pointQueryParam
        );
        if (res.status !== 200) {
          this.$message.error("");
        } else {
          // console.log(res.result);
          this.dataSource = res.result;
          this.loading= false
        }
      } else if (this.value == 3) {
        const { data: res } = await this.$http.get(
          "/graduate-project/getThirdPoint?stuEnrollmentNumber=" +
            this.pointQueryParam
        );
        if (res.status !== 200) {
          this.$message.error("");
        } else {
          // console.log(res.result);
          this.dataSource = res.result;
          this.loading= false
        }
      } else if (this.value == 4) {
        const { data: res } = await this.$http.get(
          "/graduate-project/getFourthPoint?stuEnrollmentNumber=" +
            this.pointQueryParam
        );
        if (res.status !== 200) {
          this.$message.error("");
        } else {
          // console.log(res.result);
          this.dataSource = res.result;
          this.loading= false
        }
      } else if (this.value == 5) {
        const { data: res } = await this.$http.get(
          "/graduate-project/getAllPoint?stuEnrollmentNumber=" +
            this.pointQueryParam
        );
        if (res.status !== 200) {
          this.$message.error("");
        } else {
          // console.log(res.result);
          this.dataSource = res.result;
          this.loading= false
        }
      }
    },
    // 
    async getStuPointData() {
      this.loading= true
      const { data: res } = await this.$http.get(this.url.stuInfoUrl, {
        params: this.queryPageInfo,
      });
      // console.log(res);
      if (res.status === 200) {
        this.stuInfoData = res.result;
        this.total = res.total;
        this.loading= false
      } else if (res.status === 500) {
        this.$message.error("");
      }
    },
    handleViewPoint(row) {
      this.fullscreenLoading = true
      this.voPointDialogVisible = true;
      this.pointQueryParam = row.stuEnrollmentNumber;
      this.fullscreenLoading = false
    },
    // 
    async searchStudentInfo() {
      this.loading= true
      const { data: res } = await this.$http.post(
        "/graduate-project/searchStuInfoByEnrollmentNumber",
        {
          stuEnrollmentNumber: this.stuEnrollmentNumberQuery.trim(),
          pagenum: this.queryPageInfo.pagenum,
          pagesize: this.queryPageInfo.pagesize,
        }
      );
      if (res.status === 200) {
        // console.log(total);
        this.stuInfoData = res.result;
        this.total = res.result.length;
        this.$message.success(res.message);
        this.loading= false
      }
    },
    reset() {
      this.stuEnrollmentNumberQuery = ""
      this.getStuPointData()
    },
    // 
    handleSizeChange(newSize) {
      this.queryPageInfo.pagesize = newSize;
      this.getStuPointData();
    },
    handleCurrentChange(newPage) {
      this.queryPageInfo.pagenum = newPage;
      this.getStuPointData();
    },
    handleClose(){
      this.dataSource = []
      this.value = ""
    }
  },
};
</script>

<style scoped>
</style>