<template>
  <div>
    <!--  -->
    <el-breadcrumb separator-class="el-icon-arrow-right">
      <el-breadcrumb-item :to="{ path: '/stuHome' }"></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
    </el-breadcrumb>
    <el-card>
      <!--  -->
      <el-table border stripe :data="dataSource" v-loading="loading">
        <el-table-column label="#" type="index"></el-table-column>
        <el-table-column
          label=""
          prop="stuEnrollmentNumber"
          width="200px"
        ></el-table-column>
        <el-table-column
          label=""
          prop="stuIdentifyNum"
          width="180px"
        ></el-table-column>
        <el-table-column
          label=""
          prop="stuName"
          width="180px"
        ></el-table-column>
        <el-table-column
          label=""
          prop="stuPassName"
          width="180px"
        ></el-table-column>
        <el-table-column label="" width="180px" prop="stuGender">
        </el-table-column>
        <el-table-column label="" width="180px" prop="stuAge">
        </el-table-column>
        <el-table-column label="" width="180px" prop="stuAddress">
        </el-table-column>
        <el-table-column
          label=""
          width="180px"
          prop="stuPoliticsStatus"
        >
        </el-table-column>
        <el-table-column label="" width="180px" prop="stuNativePlace">
        </el-table-column>
        <el-table-column label="" width="180px" prop="stuNation">
        </el-table-column>
        <el-table-column label="" width="180px" prop="stuPhoneNumber">
        </el-table-column>
        <el-table-column label="" width="180px" prop="stuHomeAddress">
        </el-table-column>
        <el-table-column label="" width="180px" prop="stuPostCode">
        </el-table-column>
        <el-table-column label="" width="180px" prop="stuHealth">
        </el-table-column>
        <!-- <el-table-column label="" width="180px"><el-link type="primary" icon="el-icon-s-promotion" :underline="false"></el-link></el-table-column>
        <el-table-column label="" width="180px"><el-link type="primary" icon="el-icon-s-promotion" :underline="false"></el-link></el-table-column>
        <el-table-column label="" width="180px"><el-link type="primary" icon="el-icon-s-promotion" :underline="false"></el-link></el-table-column> -->
        <el-table-column fixed="right" label="" width="100">
          <template slot-scope="scope">
            <el-link
              type="primary"
              :underline="false"
              icon="el-icon-position"
              @click="viewMyInfo(scope.row)"
              ></el-link
            >
            <el-link
              type="danger"
              :underline="false"
              icon="el-icon-link"
              @click="errorReport(scope.row)"
              ></el-link
            >
          </template>
        </el-table-column>
      </el-table>
    </el-card>

    <!--  -->
    <el-dialog
      @close="errReportDialogClosed"
      title=""
      :visible.sync="errorDialogVisible"
      width="60%"
    >
      <!--  -->
      <el-form
        :rules="errFormRules"
        :model="errReportForm"
        ref="errReportFormRef"
        label-width="170px"
      >
        <el-form-item prop="questionModule" label="">
          <el-input v-model="errReportForm.questionModule"></el-input>
        </el-form-item>
        <el-form-item prop="questionDesc" label="">
          <el-input v-model="errReportForm.questionDesc"></el-input>
        </el-form-item>
      </el-form>
      <!--  -->
      <span slot="footer" class="dialog-footer">
        <el-button type="primary" @click="submitErrReport()" v-loading.fullscreen.lock="fullscreenLoading"> </el-button>
        <el-button @click="errorDialogVisible = false"> </el-button>
      </span>
    </el-dialog>

    <!--  -->
    <el-dialog title="" :visible.sync="viewDialogVisible" :fullscreen="isFull">
      <!--  -->
      <el-form :model="viewForm" ref="viewFormRef" label-width="70px">
        <el-form-item label="">
          <el-input
            :disabled="isDisabled"
            v-model="viewForm.stuEnrollmentNumber"
          ></el-input>
        </el-form-item>
        <el-form-item label="">
          <el-input
            :disabled="isDisabled"
            v-model="viewForm.stuIdentifyNum"
          ></el-input>
        </el-form-item>
        <el-form-item label="">
          <el-input
            :disabled="isDisabled"
            v-model="viewForm.stuName"
          ></el-input>
        </el-form-item>
        <el-form-item label="">
          <el-input
            :disabled="isDisabled"
            v-model="viewForm.stuPassName"
          ></el-input>
        </el-form-item>
        <el-form-item label="">
          <el-input
            :disabled="isDisabled"
            v-model="viewForm.stuGender"
          ></el-input>
        </el-form-item>
        <el-form-item label="">
          <el-input :disabled="isDisabled" v-model="viewForm.stuAge"></el-input>
        </el-form-item>
        <el-form-item label="">
          <el-input
            :disabled="isDisabled"
            v-model="viewForm.stuAddress"
          ></el-input>
        </el-form-item>
        <el-form-item label="">
          <el-input
            :disabled="isDisabled"
            v-model="viewForm.stuPoliticsStatus"
          ></el-input>
        </el-form-item>
        <el-form-item label="" prop="stuNativePlace">
          <el-input
            :disabled="isDisabled"
            v-model="viewForm.stuNativePlace"
          ></el-input>
        </el-form-item>
        <el-form-item label="" prop="stuNation">
          <el-input
            :disabled="isDisabled"
            v-model="viewForm.stuNation"
          ></el-input>
        </el-form-item>
        <el-form-item label="">
          <el-input
            :disabled="isDisabled"
            v-model="viewForm.stuPhoneNumber"
          ></el-input>
        </el-form-item>
        <el-form-item label="">
          <el-input
            :disabled="isDisabled"
            v-model="viewForm.stuHomeAddress"
          ></el-input>
        </el-form-item>
        <el-form-item label="">
          <el-input
            :disabled="isDisabled"
            v-model="viewForm.stuPostCode"
          ></el-input>
        </el-form-item>
        <el-form-item label="">
          <el-input
            :disabled="isDisabled"
            v-model="viewForm.stuHealth"
          ></el-input>
        </el-form-item>
        <el-form-item label="">
          <el-input
            :disabled="isDisabled"
            v-model="viewForm.stuEducationExperience"
          ></el-input>
        </el-form-item>
        <el-form-item label="">
          <el-input
            :disabled="isDisabled"
            v-model="viewForm.stuHonor"
          ></el-input>
        </el-form-item>
        <el-form-item label="">
          <el-input
            :disabled="isDisabled"
            v-model="viewForm.stuPunishment"
          ></el-input>
        </el-form-item>
      </el-form>
      <!--  -->
      <span slot="footer" class="dialog-footer">
        <el-button @click="viewDialogVisible = false"> </el-button>
      </span>
    </el-dialog>
  </div>
</template>

<script>
export default {
  name: "stuPersonalInfo",
  data() {
    return {
      isFull: true,
      fullscreenLoading: false,
      loading: false,
      errFormRules: {
        questionModule: [
          { required: true, message: "", trigger: "blur" },
          {
            min: 2,
            max: 30,
            message: "230",
            trigger: "blur",
          },
        ],
        questionDesc: [
          { required: true, message: "", trigger: "blur" },
          {
            min: 2,
            max: 30,
            message: "230",
            trigger: "blur",
          },
        ],
      },
      errReportForm: {
        questionStatus: 0,
        stuEnrollmentNumber: localStorage.getItem("stu_enrollmentNumber"),
        stuName: localStorage.getItem("stu_name"),
        questionModule: "",
        questionDesc: "",
      },
      // 
      errorDialogVisible: false,
      isDisabled: true,
      viewForm: {
        stuEnrollmentNumber: "",
        stuIdentifyNum: "",
        stuName: "",
        stuPassName: "",
        stuGender: "",
        stuAge: "",
        stuAddress: "",
        stuNativePlace: "",
        stuNation: "",
        stuPhoneNumber: "",
        stuHomeAddress: "",
        stuPostCode: "",
        stuHealth: "",
        stuEducationExperience: "",
        stuPoliticsStatus: "",
        stuHonor: "",
        stuPunishment: "",
      },
      // 
      viewDialogVisible: false,
      dataSource: [],
      url: {
        stuInfoUrl:
          "/graduate-project/viewStuInfo?stuEnrollmentNumber=" +
          localStorage.getItem("stu_enrollmentNumber"),
      },
    };
  },
  mounted() {
    this.getStuInfo();
  },
  methods: {
    submitErrReport() {
      this.$refs.errReportFormRef.validate(async (valid) => {
        if (!valid) return;
         this.fullscreenLoading = true
        const { data: res } = await this.$http.post(
          "/graduate-project/saveErrReport",
          this.errReportForm
        );
        if (res.status !== 200) {
          this.$message.err("");
        }
        this.switchErrStatus()
        this.$message.success(res.message);
        this.errReportDialogClosed()
         this.fullscreenLoading = false
      });
    },
    // 
    async switchErrStatus() {
     const {data:res}= await this.$http.get(
        "/graduate-project/switchErrStatus?stuEnrollmentNumber=" +
          localStorage.getItem("stu_enrollmentNumber")
      );
      if(res.status !== 200) {
        this.$message.error(res.message)
      }
    },
    errReportDialogClosed() {
      this.errorDialogVisible = false
      this.$refs.errReportFormRef.resetFields();
    },
    // 
    errorReport(row) {
      this.errorDialogVisible = true;
    },
    // 
    async viewMyInfo(row) {
      this.loading= true
      this.viewDialogVisible = true;
      const { data: res } = await this.$http.get(
        "/graduate-project/viewStuPersonalInfo?stuEnrollmentNumber=" +
          row.stuEnrollmentNumber
      );
      if (res.status !== 200) {
        this.$message.error("");
      }
      if (res.result[0].stuGender === 0) {
        res.result[0].stuGender = "";
      } else {
        res.result[0].stuGender = "";
      }
      if (res.result[0].stuPoliticsStatus === 0) {
        res.result[0].stuPoliticsStatus = "";
      } else if (res.result[0].stuPoliticsStatus === 1) {
        res.result[0].stuPoliticsStatus = "";
      } else {
        res.result[0].stuPoliticsStatus = "";
      }
      if (res.result[0].stuHealth == 0) {
        res.result[0].stuHealth = "";
      } else if (res.result[0].stuHealth == 1) {
        res.result[0].stuHealth = "";
      } else {
        res.result[0].stuHealth = "";
      }
      if (res.result[0].stuHonor === 0) {
        res.result[0].stuHonor = "";
      } else {
        res.result[0].stuHonor = "--->";
      }
      if (res.result[0].stuPunishment === 0) {
        res.result[0].stuPunishment = "";
      } else {
        res.result[0].stuPunishment = "--->";
      }
      this.viewForm = res.result[0];
      this.loading= false
    },
    async getStuInfo() {
      this.loading= true
      const { data: res } = await this.$http.get(this.url.stuInfoUrl);
      // console.log(res.student);
      if (res.status === 200) {
        this.dataSource = res.result;
        this.dataSource.forEach((value, key) => {
          // console.log(value);
          if (value.stuGender === 0) {
            value.stuGender = "";
          } else {
            value.stuGender = "";
          }
          if (value.stuHealth == 0) {
            value.stuHealth = "";
          } else if (value.stuHealth == 1) {
            value.stuHealth = "";
          } else if (value.stuHealth == -1) {
            value.stuHealth = "";
          }
          if (value.stuPoliticsStatus === 0) {
            value.stuPoliticsStatus = "";
          } else if (value.stuPoliticsStatus === 1) {
            value.stuPoliticsStatus = "";
          } else if (value.stuPoliticsStatus === 2) {
            value.stuPoliticsStatus = "";
          }
        });
        this.loading= false
        // console.log(this.dataSource);
        // this.dataSource = res.result;
      } else if (res.status === 500) {
        this.$message.console.error("");
      }
    },
  },
};
</script>

<style scoped>
.el-table th > .cell {
  text-align: center;
}

.el-table .cell {
  text-align: center;
}
</style>
