<template>
  <div>
    <!--  -->
    <el-breadcrumb separator-class="el-icon-arrow-right">
      <el-breadcrumb-item :to="{ path: '/home' }"></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
    </el-breadcrumb>

    <!--  -->
    <el-card>
      <el-row :gutter="20">
        <el-col :span="5">
          <!--  -->
          <el-input
            :clearable="true"
            v-model="queryInfo.stuEnrollmentNumber"
            placeholder=""
          >
          </el-input>
        </el-col>
        <el-col :span="5">
          <!--  -->
          <el-input
            :clearable="true"
            v-model="queryInfo.stuName"
            placeholder=""
          >
          </el-input>
        </el-col>
        <el-col :span="8">
          <el-button
            type="primary"
            icon="el-icon-search"
            @click="searchStuInfo()"
            ></el-button
          >
          <el-button
            type="warning"
            icon="el-icon-refresh-right"
            @click="resetFileds()"
            ></el-button
          >
          <el-upload
            style="margin-top: -40px; margin-left: 200px"
            :auto-upload="false"
            action="#"
            :limit="1"
            :on-change="handleUpload"
            ref="upload"
          >
            <el-button type="success" icon="el-icon-upload2"
              ></el-button
            >
          </el-upload>
        </el-col>
      </el-row>

      <!--  -->
      <el-table border stripe :data="dataSource" v-loading="isLoading">
        <el-table-column label="#" type="index"></el-table-column>
        <el-table-column
          label=""
          prop="stuEnrollmentNumber"
          width="200px"
        ></el-table-column>
        <el-table-column
          label=""
          prop="stuIdentifyNum"
          width="180px"
        ></el-table-column>
        <el-table-column
          label=""
          prop="stuName"
          width="180px"
        ></el-table-column>
        <el-table-column
          label=""
          prop="stuPassName"
          width="180px"
        ></el-table-column>
        <el-table-column label="" width="180px" prop="stuGender">
        </el-table-column>
        <el-table-column label="" width="180px" prop="stuAge">
        </el-table-column>
        <el-table-column label="" width="180px" prop="stuAddress">
        </el-table-column>
        <el-table-column
          label=""
          width="180px"
          prop="stuPoliticsStatus"
        >
        </el-table-column>
        <el-table-column label="" width="180px" prop="stuNativePlace">
        </el-table-column>
        <el-table-column label="" width="180px" prop="stuNation">
        </el-table-column>
        <el-table-column label="" width="180px" prop="stuPhoneNumber">
        </el-table-column>
        <el-table-column label="" width="180px" prop="stuHomeAddress">
        </el-table-column>
        <el-table-column label="" width="180px" prop="stuPostCode">
        </el-table-column>
        <el-table-column label="" width="180px" prop="stuHealth">
        </el-table-column>
        <!-- <el-table-column label="" width="180px"
          ><el-link type="primary" icon="el-icon-s-promotion" :underline="false"
            ></el-link
          ></el-table-column
        >
        <el-table-column label="" width="180px"
          ><el-link type="primary" icon="el-icon-s-promotion" :underline="false"
            ></el-link
          ></el-table-column
        >
        <el-table-column label="" width="180px"
          ><el-link type="primary" icon="el-icon-s-promotion" :underline="false" @click="handleViewPunish(scope.row)"
            ></el-link
          ></el-table-column
        > -->
        <el-table-column fixed="right" label="" width="100">
          <template slot-scope="scope">
            <!-- <el-link
              type="primary"
              :underline="false"
              icon="el-icon-edit"
              @click="handleEdit(scope.row)"
              ></el-link
            > -->
            <el-link
              type="danger"
              :underline="false"
              icon="el-icon-delete"
              @click="handleDelete(scope.row)"
              ></el-link
            >
          </template>
        </el-table-column>
      </el-table>

      <!--  -->
      <el-pagination
        :page-sizes="[5, 10, 15, 20]"
        layout="total, sizes, prev, pager, next, jumper"
        :total="total"
        :current-page="queryPageInfo.pagenum"
        :page-size="queryPageInfo.pagesize"
        @size-change="handleSizeChange"
        @current-change="handleCurrentChange"
      >
      </el-pagination>

      <!-- <img :src="AvartarData.src1" alt="" id="avartar"> -->
    </el-card>
    <!--  -->
    <el-dialog
      @close="editDialogClosed"
      title=""
      :visible.sync="editDialogVisible"
      width="60%"
    >
      <!--  -->
      <el-form
        :model="editForm"
        :rules="editFormRules"
        ref="editFormRef"
        label-width="100px"
      >
        <el-form-item label="" prop="stuEnrollmentNumber">
          <el-input v-model="editForm.stuEnrollmentNumber"></el-input>
        </el-form-item>
        <el-form-item label="" prop="stuIdentifyNum">
          <el-input v-model="editForm.stuIdentifyNum"></el-input>
        </el-form-item>
        <el-form-item label="" prop="stuName">
          <el-input v-model="editForm.stuName"></el-input>
        </el-form-item>
        <el-form-item label="" prop="stuPassName">
          <el-input v-model="editForm.stuPassName"></el-input>
        </el-form-item>
        <el-form-item label="" prop="stuGender">
          <el-select v-model="editForm.stuGender" placeholder="">
            <el-option label="" value="man"></el-option>
            <el-option label="" value="women"></el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="" prop="stuAge">
          <el-input v-model="editForm.stuAge"></el-input>
        </el-form-item>
        <el-form-item label="" prop="stuAddress">
          <el-input v-model="editForm.stuAddress"></el-input>
        </el-form-item>
        <el-form-item label="" prop="stuPoliticsStatus">
          <el-select
            v-model="editForm.stuPoliticsStatus"
            placeholder=""
          >
            <el-option label="" value="socialman"></el-option>
            <el-option label="" value="leaguemember"></el-option>
            <el-option label="" value="chinamember"></el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="" prop="stuNativePlace">
          <el-input v-model="editForm.stuNativePlace"></el-input>
        </el-form-item>
        <el-form-item label="" prop="stuNation">
          <el-input v-model="editForm.stuNation"></el-input>
        </el-form-item>
        <el-form-item label="" prop="stuPhoneNumber">
          <el-input v-model="editForm.stuPhoneNumber"></el-input>
        </el-form-item>
        <el-form-item label="" prop="stuHomeAddress">
          <el-input v-model="editForm.stuHomeAddress"></el-input>
        </el-form-item>
        <el-form-item label="" prop="stuPostCode">
          <el-input v-model="editForm.stuPostCode"></el-input>
        </el-form-item>
        <el-form-item label="" prop="stuHealth">
          <el-select v-model="editForm.stuHealth" placeholder="">
            <el-option label="" value="health"></el-option>
            <el-option label="" value="better"></el-option>
            <el-option label="" value="nothealth"></el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="" prop="stuEducationExperience">
          <el-input v-model="editForm.stuEducationExperience"></el-input>
        </el-form-item>
        <el-form-item label="" prop="stuHonor">
          <el-input v-model="editForm.stuHonor"></el-input>
        </el-form-item>
        <el-form-item label="" prop="stuPunishment">
          <el-input v-model="editForm.stuPunishment"></el-input>
        </el-form-item>
      </el-form>
      <!--  -->
      <span slot="footer" class="dialog-footer">
        <el-button @click="editDialogVisible = false"> </el-button>
        <el-button type="primary" @click="editInfo()"> </el-button>
      </span>
    </el-dialog>
  </div>
</template>

<script>
import XLSX from "../../../node_modules/xlsx/xlsx";
export default {
  name: "Welcome",
  data() {
    return {
      AvartarData: {
        src1: "https://z3.ax1x.com/2021/05/01/gVXtxS.jpg",
      },
      // 
      editDialogVisible: false,

      // 
      editForm: {},

      // 
      editFormRules: {
        //
        stuEnrollmentNumber: [
          { required: true, message: "", trigger: "blur" },
          {
            min: 20,
            max: 20,
            message: " 20 ",
            trigger: "blur",
          },
        ],
        // 
        stuIdentifyNum: [
          { required: true, message: "", trigger: "blur" },
          {
            min: 18,
            max: 18,
            message: " 18 ",
            trigger: "blur",
          },
        ],
        // 
        stuName: [
          { required: true, message: "", trigger: "blur" },
          {
            min: 2,
            max: 8,
            message: "28",
            trigger: "blur",
          },
        ],
        // 
        stuPassName: [
          { required: false },
          {
            min: 2,
            max: 8,
            message: "28",
            trigger: "blur",
          },
        ],
        // 
        stuGender: [{ required: true, message: "", trigger: "blur" }],
        // 
        stuAddress: [
          { required: true, message: "", trigger: "blur" },
          {
            min: 4,
            max: 40,
            message: "440",
            trigger: "blur",
          },
        ],
        // 
        stuPoliticsStatus: [
          { required: true, message: "", trigger: "blur" },
        ],
        // 
        stuNativePlace: [
          { required: true, message: "", trigger: "blur" },
          {
            min: 3,
            max: 12,
            message: "312",
            trigger: "blur",
          },
        ],
        // 
        stuNation: [
          { required: true, message: "", trigger: "blur" },
          {
            min: 2,
            max: 12,
            message: "212",
            trigger: "blur",
          },
        ],
        // 
        stuPhoneNumber: [
          { required: true, message: "", trigger: "blur" },
          {
            min: 11,
            max: 11,
            message: "11",
            trigger: "blur",
          },
        ],
        // 
        stuHomeAddress: [
          { required: true, message: "", trigger: "blur" },
          {
            min: 4,
            max: 40,
            message: "440",
            trigger: "blur",
          },
        ],
        // 
        stuPostCode: [
          { required: true, message: "", trigger: "blur" },
          {
            min: 6,
            max: 6,
            message: "6",
            trigger: "blur",
          },
        ],
        // 
        stuHealth: [
          { required: true, message: "", trigger: "blur" },
        ],
        // 
        stuEducationExperience: [
          { required: true, message: "", trigger: "blur" },
          {
            min: 10,
            message: "10",
            trigger: "blur",
          },
        ],
        // 
        stuName: [
          { required: true, message: "", trigger: "blur" },
          {
            min: 2,
            max: 8,
            message: "28",
            trigger: "blur",
          },
        ],
      },
      total: 0,
      // 
      queryPageInfo: {
        pagenum: 1,
        pagesize: 5,
      },
      // 
      queryInfo: {
        stuName: "",
        stuEnrollmentNumber: "",
      },
      isLoading: false,
      dataSource: [],
      url: {
        stuInfoUrl: "/graduate-project/stuInfo",
        searchStuInfoByNameUrl: "/graduate-project/searchStuInfoByName",
        searchStuInfoByEnrollmentNumber:
          "/graduate-project/searchStuInfoByEnrollmentNumber",
        searchStuInfo: "/graduate-project/searchStuInfo",
      },
      // 
      excelData: [],
    };
  },
  mounted() {
    this.getStuInfoList();
    // this.$emit("getStuInfoList", this.getStuInfoList());
  },
  methods: {
    // 
    async handleUpload(file) {
      let that = this;
      // Excel
      if (
        file.raw.type !=
          "application/vnd.openxmlformats-officedocument.spreadsheetml.sheet" &&
        file.raw.type != "application/vnd.ms-excel"
      ) {
        this.$notify.error({
          title: "",
          message: "(Excel)",
        });
        this.$refs.upload.clearFiles();
        return false;
      }
      //
      let reader = new FileReader();
      let flag =
        typeof FileReader !== "undefined" &&
        (FileReader.prototype || {}).readAsBinaryString;
      if (flag) {
        reader.readAsBinaryString(file.raw);
      } else {
        reader.readAsArrayBuffer(file.raw);
      }
      reader.onload = await function (e) {
        let data = e.target.result;
        let workBook = XLSX.read(data, { type: "binary" });
        workBook.SheetNames.forEach(function (name) {
          var XL_row_object = XLSX.utils.sheet_to_row_object_array(
            workBook.Sheets[name]
          );
          if (XL_row_object.length > 0) {
            that.excelData = JSON.parse(JSON.stringify(XL_row_object));
          }
          console.log(that.excelData);
          // json
          // that.excelData.forEach((item, index) => {
          //   let obj = {};
          //   for (let i in item) {
          //     if (i !== "stuEnrollmentNumber" && i !== "stuName") {
          //       obj[i] = item[i];
          //       delete item[i];
          //     }
          //   }
          //   item.grade = obj;
          // });
          that.isLoading = true;
          that.$http
            .post("/graduate-project/importStuInfo", that.excelData)
            .then((res) => {
              if (res.data.success) {
                that.$refs.upload.clearFiles();
                that.$message.success("");
                that.getStuInfoList();
                that.isLoading = false;
              } else {
                that.$refs.upload.clearFiles();
                that.$message.error("");
              }
            })
            .catch(() => {
              that.$refs.upload.clearFiles();
              that.$notify.error({
                title: "",
                message: "",
              });
              that.isLoading = false
            });
        });
      };
    },
    // 
    editDialogClosed() {
      this.$refs.editFormRef.resetFields();
    },

    // 
    editInfo() {
      if (this.editForm.stuGender === "man") {
        this.editForm.stuGender = Number(0);
      } else if (this.editForm.stuGender === "women") {
        this.editForm.stuGender = Number(1);
      }
      if (this.editForm.stuPoliticsStatus === "socialman") {
        this.editForm.stuPoliticsStatus = Number(0);
      } else if (this.editForm.stuPoliticsStatus === "leaguemember") {
        this.editForm.stuPoliticsStatus = Number(1);
      } else if (this.editForm.stuPoliticsStatus === "chinamember") {
        this.editForm.stuPoliticsStatus = Number(2);
      }
      if (this.editForm.stuHealth === "health") {
        this.editForm.stuHealth = Number(0);
      } else if (this.editForm.stuHealth === "better") {
        this.editForm.stuHealth = Number(1);
      } else if (this.editForm.stuHealth === "nohealth") {
        this.editForm.stuHealth = Number(-1);
      }
      if (this.editForm.stuHonor === "") {
        this.editForm.stuHonor = Number(0);
      } else {
        this.editForm.stuHonor = Number(1);
      }
      if (this.editForm.stuPunishment === "") {
        this.editForm.stuPunishment = Number(0);
      } else {
        this.editForm.stuPunishment = Number(1);
      }
      // console.log(this.editForm);
      this.$refs.editFormRef.validate(async (valid) => {
        if (!valid) return;
        const confirmResult = await this.$confirm(
          "?",
          "",
          {
            confirmButtonText: "",
            cancelButtonText: "",
            type: "warning",
          }
        ).catch((err) => err);
        if (confirmResult !== "confirm") {
          return this.$message.info("");
        }
        const { data: res } = await this.$http.post(
          "/graduate-project/editStuInfo",
          this.editForm
        );
        if (res.status !== 200) {
          this.$message.error("");
        }
        // console.log(res.result);
        this.$message.success(res.message);
        this.editDialogVisible = false;
        this.getStuInfoList();
      });
    },
    // 
    handleEdit(row) {
      this.editDialogVisible = true;
      if (row.stuGender === "") {
        row.stuGender = Number(0);
      } else if (row.stuGender === "") {
        row.stuGender = Number(1);
      }
      if (row.stuPoliticsStatus === "") {
        row.stuPoliticsStatus = Number(0);
      } else if (row.stuPoliticsStatus === "") {
        row.stuPoliticsStatus = Number(1);
      } else if (row.stuPoliticsStatus === "") {
        row.stuPoliticsStatus = Number(2);
      }
      if (row.stuHealth === "") {
        row.stuHealth = Number(0);
      } else if (row.stuHealth === "") {
        row.stuHealth = Number(1);
      } else if (row.stuHealth === "") {
        row.stuHealth = Number(-1);
      }
      if (row.stuHonor === "") {
        row.stuHonor = Number(0);
      } else {
        row.stuHonor = Number(1);
      }
      if (row.stuPunishment === "") {
        row.stuPunishment = Number(0);
      } else {
        row.stuPunishment = Number(1);
      }
      this.editForm = row;
      // console.log(this.editForm);
    },
    // 
    handleSizeChange(newSize) {
      this.queryPageInfo.pagesize = newSize;
      this.getStuInfoList();
    },
    handleCurrentChange(newPage) {
      this.queryPageInfo.pagenum = newPage;
      this.getStuInfoList();
    },
    // 
    resetFileds() {
      this.isLoading = true;
      this.queryInfo.stuName = "";
      this.queryInfo.stuEnrollmentNumber = "";
      this.getStuInfoList();
      this.isLoading = false;
    },
    // 
    async searchStuInfo() {
      this.isLoading = true;
      const { data: res } = await this.$http.post(
        this.url.searchStuInfo,
        this.queryInfo
      );
      if (res.status === 200) {
        // console.log(res);
        this.dataSource = res.result;
        this.total = res.total;
        this.$message.success(res.message);
        this.isLoading = false;
      }
    },
    // 
    async getStuInfoList() {
      this.isLoading = true;
      const { data: res } = await this.$http.get(this.url.stuInfoUrl, {
        params: this.queryPageInfo,
      });
      // console.log(res);
      if (res.status === 200) {
        this.dataSource = res.result;
        // this.$message.success(res.message);
        this.total = res.total;
        this.dataSource.forEach((value, key) => {
          // console.log(value);
          if (value.stuGender === 0) {
            value.stuGender = "";
          } else {
            value.stuGender = "";
          }
          if (value.stuHealth == 0) {
            value.stuHealth = "";
          } else if (value.stuHealth == 1) {
            value.stuHealth = "";
          } else if (value.stuHealth == -1) {
            value.stuHealth = "";
          }
          if (value.stuPoliticsStatus === 0) {
            value.stuPoliticsStatus = "";
          } else if (value.stuPoliticsStatus === 1) {
            value.stuPoliticsStatus = "";
          } else if (value.stuPoliticsStatus === 2) {
            value.stuPoliticsStatus = "";
          }
        });
        // console.log(res.result[0].stuAvatar);
        // this.AvartarData.src1 = res.result[0].stuAvatar
        this.isLoading = false;
        // console.log(this.dataSource);
        // this.dataSource = res.result;
      } else if (res.status === 500) {
        this.$message.error("");
      }
    },
    // 
    async handleDelete(row) {
      // this.isLoading = true;
      //(confirmcancel)
      const confirmResult = await this.$confirm(
        ", ?",
        "",
        {
          confirmButtonText: "",
          cancelButtonText: "",
          type: "warning",
        }
      ).catch((err) => err);

      if (confirmResult !== "confirm") {
        return this.$message.info("");
      } else {
        const confirmResult = await this.$confirm(
          "?",
          "",
          {
            confirmButtonText: "",
            cancelButtonText: "",
            type: "warning",
          }
        ).catch((err) => err);
        if (confirmResult !== "confirm") {
          return this.$message.info("");
        }
        this.isLoading = true;
        const { data: res } = await this.$http.delete(
          "/graduate-project/deleteStuInfo?stuEnrollmentNumber=" +
            row.stuEnrollmentNumber
        );
        if (res.status !== 200) {
          return this.$message.error("");
        }
        this.$message.success("");
        this.getStuInfoList();
        this.isLoading = false;
      }
    },
  },
};
</script>

<style lang="less" scoped>
/deep/.el-table th > .cell {
  text-align: center;
}

/deep/.el-table .cell {
  text-align: center;
}
body {
  margin: 0;
}
#avartar {
  height: 200px;
  width: 200px;
  background-color: blue;
}
</style>
