<template>
  <div>
    <!--  -->
    <el-breadcrumb separator-class="el-icon-arrow-right">
      <el-breadcrumb-item :to="{ path: '/stuHome' }"></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
    </el-breadcrumb>
    <el-card>


          <span></span>
          <el-select v-model="value" clearable placeholder="">
            <el-option
              v-for="item in options"
              :key="item.value"
              :label="item.label"
              :value="item.value"
            >
            </el-option>
          </el-select>


          <!--  -->
          <el-button type="primary" style="margin-left: 8px" icon="el-icon-search" @click="getPoint()"
            ></el-button
          >

          <!--  -->
          <el-button type="primary" @click="exportData" icon="el-icon-download"
            ></el-button
          >


      <!--  -->
      <el-table border stripe :data="dataSource" v-loading="loading">
        <el-table-column label="#" type="index"></el-table-column>
        <el-table-column label="" prop="stuSubjectName"></el-table-column>
        <el-table-column label="" prop="stuCredit"></el-table-column>
        <el-table-column label="" prop="stuGpa"></el-table-column>
        <el-table-column label="" prop="stuGrade"></el-table-column>
      </el-table>
    </el-card>
  </div>
</template>

<script>
export default {
  name: "stuPrintExcel",
  data() {
    return {
      loading:false,
      options: [
        {
          value: "1",
          label: "",
        },
        {
          value: "2",
          label: "",
        },
        {
          value: "3",
          label: "",
        },
        {
          value: "4",
          label: "",
        },
        {
          value: "5",
          label: "",
        },
      ],
      value: "",
      // 
      dataSource: [],
    };
  },
  methods: {
    // 
    exportData() {
      // console.log(this.dataSource);
      require.ensure([], () => {
        const { export_json_to_excel } = require("../../excel/Export2Excel");
        const tHeader = ["", "", "", ""];
        const filterVal = ["stuSubjectName", "stuCredit", "stuGpa", "stuGrade"];
        const list = this.dataSource;
        const data = this.formatJson(filterVal, list);
        export_json_to_excel(tHeader, data, "");
      });
    },
    formatJson(filterVal, jsonData) {
      return jsonData.map((v) => filterVal.map((j) => v[j]));
    },
    async getPoint() {
      // console.log(localStorage.getItem("stu_passKey"));
      this.loading= true
      // console.log(this.value);
      if (this.value === "") {
        this.$message.warning("");
        this.loading=false
      } else if (this.value == 1) {
        const { data: res } = await this.$http.get(
          "/graduate-project/getFirstPoint?stuEnrollmentNumber=" +
            localStorage.getItem("stu_enrollmentNumber")
        );
        if (res.status !== 200) {
          this.$message.error("");
        } else {
          // console.log(res.result);
          this.dataSource = res.result;
          this.loading=false
        }
      } else if (this.value == 2) {
        const { data: res } = await this.$http.get(
          "/graduate-project/getSecondPoint?stuEnrollmentNumber=" +
            localStorage.getItem("stu_enrollmentNumber")
        );
        if (res.status !== 200) {
          this.$message.error("");
        } else {
          // console.log(res.result);
          this.dataSource = res.result;
          this.loading=false
        }
      } else if (this.value == 3) {
        const { data: res } = await this.$http.get(
          "/graduate-project/getThirdPoint?stuEnrollmentNumber=" +
            localStorage.getItem("stu_enrollmentNumber")
        );
        if (res.status !== 200) {
          this.$message.error("");
        } else {
          // console.log(res.result);
          this.dataSource = res.result;
          this.loading=false
        }
      } else if (this.value == 4) {
        const { data: res } = await this.$http.get(
          "/graduate-project/getFourthPoint?stuEnrollmentNumber=" +
            localStorage.getItem("stu_enrollmentNumber")
        );
        if (res.status !== 200) {
          this.$message.error("");
        } else {
          // console.log(res.result);
          this.dataSource = res.result;
          this.loading=false
        }
      } else if (this.value == 5) {
        const { data: res } = await this.$http.get(
          "/graduate-project/getAllPoint?stuEnrollmentNumber=" +
            localStorage.getItem("stu_enrollmentNumber")
        );
        if (res.status !== 200) {
          this.$message.error("");
        } else {
          // console.log(res.result);
          this.dataSource = res.result;
          this.loading=false
        }
      }
    },
  },
};
</script>

<style scoped>
</style>
