<template>
  <el-container class="home-container">
    <!--  -->
    <el-header>
      <div>
        <img :src="avartarSrc" alt="">
        <span></span>
      </div>
      <el-button
        type="danger"
        @click="logout"
        plain
        icon="el-icon-switch-button"
        ></el-button
      >
    </el-header>
    <!--  -->
    <el-container style="height: 100vh">
      <!--  -->
      <el-aside :width="isCollapse ? '64px' : '200px'" style="height: 100vh">
        <div class="toggle-button" @click="toggleCollapse">|||</div>

        <!--  -->
        <el-menu
          :collapse="isCollapse"
          :router="true"
          :default-active="activePath"
          :collapse-transition="false"
        >
          <el-menu-item index="/stuWelcome">
            <i class="el-icon-s-platform"></i>
            <span slot="title"></span>
          </el-menu-item>
          <el-menu-item index="/stuPersonalInfo">
            <i class="el-icon-menu"></i>
            <span slot="title"></span>
          </el-menu-item>
          <el-menu-item index="/stuPersonalReport">
            <i class="el-icon-s-finance"></i>
            <span slot="title"></span>
          </el-menu-item>
          <el-menu-item index="/stuPersonalHonor">
            <i class="el-icon-user-solid"></i>
            <span slot="title"></span>
          </el-menu-item>
          <el-menu-item index="/stuPersonalPublish">
            <i class="el-icon-s-release"></i>
            <span slot="title"></span>
          </el-menu-item>
          <el-menu-item index="/stuPrintExcel">
            <i class="el-icon-download"></i>
            <span slot="title"></span>
          </el-menu-item>
          <!-- <el-menu-item index="/stuMaterials">
            <i class="el-icon-star-on"></i>
            <span slot="title"></span> -->
          </el-menu-item>
          <el-menu-item index="/passwordService">
            <i class="el-icon-s-tools"></i>
            <span slot="title"></span>
          </el-menu-item>
        </el-menu>
      </el-aside>

      <!--  -->
      <el-main style="height: 100vh">
        <!--  -->
        <router-view></router-view>
      </el-main>
    </el-container>
  </el-container>
</template>

<script>
export default {
  data() {
    return {
      // src
      avartarSrc: "",
      // 
      isCollapse: false,
      // 
      activePath: "/stuWelcome",
    };
  },
  mounted() {
    this.avartarSrc= localStorage.getItem("avartar_src")
    // console.log(this.avartarSrc);
  },
  created() {
    this.avartarSrc= localStorage.getItem("avartar_src")
  },
  methods: {
    async logout() {
      // 
      const confirmResult = await this.$confirm(
        ""+localStorage.getItem('stu_name')+",?",
        "",
        {
          confirmButtonText: "",
          cancelButtonText: "",
          type: "warning",
        }
      ).catch((err) => err);
      if (confirmResult !== "confirm") {
        return this.$message.info("");
      }
      //token
      window.sessionStorage.clear();
      this.$message.success("");
      this.$router.push("/login");
    },
    // 
    toggleCollapse() {
      this.isCollapse = !this.isCollapse;
    },
  },
};
</script>

<style lang="less" scoped>
.el-header {
  background: linear-gradient(to right, #70e1f5, #ffd194);
  display: flex;
  justify-content: space-between;
  padding-left: 10px;
  align-items: center;
  color: #fff;
  font-size: 20px;
  > div {
    display: flex;
    align-items: center;
    span {
      margin-left: 15px;
    }
    img {
      border-radius: 50%;
      width: 40px;
      height: 40px;
    }
  }
}
.home-container {
  height: 100%;
}
.el-aside {
  background: linear-gradient(to right, #70e1f5, #ffd194);
  height: 92vh;
  .el-menu {
    background: linear-gradient(to right, #70e1f5, #ffd194);
    background-color: "#333744";
    text-color: "#fff";
    active-text-color: "#409EFF";
    border-right: none;
  }
}
.el-main {
  background: linear-gradient(to right, #70e1f5, #ffd194);
}
.iconfont {
  margin-right: 10px;
}
.toggle-button {
  background: linear-gradient(to right, #70e1f5, #ffd194);
  font-size: 10px;
  line-height: 24px;
  color: #fff;
  text-align: center;
  letter-spacing: 0.2em;
  cursor: pointer;
}
</style>
