<template>
  <div>
    <!--  -->
    <el-breadcrumb separator-class="el-icon-arrow-right">
      <el-breadcrumb-item :to="{ path: '/home' }"></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
    </el-breadcrumb>

    <!--  -->
    <el-card>
      <el-form
        ref="addFormRules"
        label-width="80px"
        :rules="addFormRules"
        :model="addInfo"
      >
        <el-form-item label="" prop="stuEnrollmentNumber">
          <el-input v-model="addInfo.stuEnrollmentNumber"></el-input>
        </el-form-item>
        <el-form-item label="" prop="stuIdentifyNum">
          <el-input v-model="addInfo.stuIdentifyNum"></el-input>
        </el-form-item>
        <el-form-item label="" prop="stuName">
          <el-input v-model="addInfo.stuName"></el-input>
        </el-form-item>
        <el-form-item label="" prop="stuPassName">
          <el-input v-model="addInfo.stuPassName"></el-input>
        </el-form-item>
        <el-form-item label="" prop="stuGender">
          <el-select v-model="addInfo.stuGender" placeholder="">
            <el-option label="" value="man"></el-option>
            <el-option label="" value="women"></el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="" prop="stuAge">
          <el-input v-model="addInfo.stuAge"></el-input>
        </el-form-item>
        <el-form-item label="" prop="stuAddress">
          <el-input v-model="addInfo.stuAddress"></el-input>
        </el-form-item>
        <el-form-item label="" prop="stuPoliticsStatus">
          <el-select
            v-model="addInfo.stuPoliticsStatus"
            placeholder=""
          >
            <el-option label="" value="socialman"></el-option>
            <el-option label="" value="leaguemember"></el-option>
            <el-option label="" value="chinamember"></el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="" prop="stuNativePlace">
          <el-input v-model="addInfo.stuNativePlace"></el-input>
        </el-form-item>
        <el-form-item label="" prop="stuNation">
          <el-input v-model="addInfo.stuNation"></el-input>
        </el-form-item>
        <el-form-item label="" prop="stuPhoneNumber">
          <el-input v-model="addInfo.stuPhoneNumber"></el-input>
        </el-form-item>
        <el-form-item label="" prop="stuHomeAddress">
          <el-input v-model="addInfo.stuHomeAddress"></el-input>
        </el-form-item>
        <el-form-item label="" prop="stuPostCode">
          <el-input v-model="addInfo.stuPostCode"></el-input>
        </el-form-item>
        <el-form-item label="" prop="stuHealth">
          <el-select v-model="addInfo.stuHealth" placeholder="">
            <el-option label="" value="health"></el-option>
            <el-option label="" value="better"></el-option>
            <el-option label="" value="nothealth"></el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="" prop="stuEducationExperience">
          <el-input v-model="addInfo.stuEducationExperience"></el-input>
        </el-form-item>
        <el-form-item label="" prop="stuHonor">
          <el-input v-model="addInfo.stuHonor"></el-input>
        </el-form-item>
        <el-form-item label="" prop="stuPunishment">
          <el-input v-model="addInfo.stuPunishment"></el-input>
        </el-form-item>
        <!-- <el-form-item label="">
          <el-link type="primary"></el-link>
        </el-form-item>
        <el-form-item label="">
          <el-link type="primary"></el-link>
        </el-form-item>
        <el-form-item label="">
          <el-link type="primary"></el-link>
        </el-form-item>
          <el-button type="primary"></el-button>
        </el-form-item> -->
      </el-form>
      <div style="float: right; margin-bottom: 10px">
        <el-button
          type="primary"
          @click="addStudentInfo()"
          v-loading.fullscreen.lock="fullscreenLoading"
          icon="el-icon-circle-plus"
          ></el-button
        >
        <el-button
          type="warning"
          @click="resetInfo('addFormRules')"
          icon="el-icon-refresh-right"
          ></el-button
        >
      </div>
    </el-card>
  </div>
</template>

<script>
export default {
  name: "Welcome",
  data() {
    return {
      fullscreenLoading: false,
      // 
      addInfo: {
        stuEnrollmentNumber: "",
        stuIdentifyNum: "",
        stuName: "",
        stuPassName: "",
        stuGender: "",
        stuAge: "",
        stuAddress: "",
        stuPoliticsStatus: "",
        stuNativePlace: "",
        stuNation: "",
        stuPhoneNumber: "",
        stuHomeAddress: "",
        stuPostCode: "",
        stuHealth: "",
        stuEducationExperience: "",
        stuHonor: "",
        stuPunishment: "",
      },
      //
      addFormRules: {
        //
        stuEnrollmentNumber: [
          { required: true, message: "", trigger: "blur" },
          {
            min: 20,
            max: 20,
            message: " 20 ",
            trigger: "blur",
          },
        ],
        // 
        stuIdentifyNum: [
          { required: true, message: "", trigger: "blur" },
          {
            min: 18,
            max: 18,
            message: " 18 ",
            trigger: "blur",
          },
        ],
        // 
        stuName: [
          { required: true, message: "", trigger: "blur" },
          {
            min: 2,
            max: 8,
            message: "28",
            trigger: "blur",
          },
        ],
        // 
        stuPassName: [
          { required: false },
          {
            min: 2,
            max: 8,
            message: "28",
            trigger: "blur",
          },
        ],
        // 
        stuGender: [{ required: true, message: "", trigger: "blur" }],
        // 
        stuAge: [
          { required: true, message: "", trigger: "blur" },
          {
            min: 2,
            max: 2,
            message: "10-100",
            trigger: "blur",
          },
        ],
        // 
        stuAddress: [
          { required: true, message: "", trigger: "blur" },
          {
            min: 4,
            max: 40,
            message: "440",
            trigger: "blur",
          },
        ],
        // 
        stuPoliticsStatus: [
          { required: true, message: "", trigger: "blur" },
        ],
        // 
        stuNativePlace: [
          { required: true, message: "", trigger: "blur" },
          {
            min: 3,
            max: 12,
            message: "312",
            trigger: "blur",
          },
        ],
        // 
        stuNation: [
          { required: true, message: "", trigger: "blur" },
          {
            min: 2,
            max: 12,
            message: "212",
            trigger: "blur",
          },
        ],
        // 
        stuPhoneNumber: [
          { required: true, message: "", trigger: "blur" },
          {
            min: 11,
            max: 11,
            message: "11",
            trigger: "blur",
          },
        ],
        // 
        stuHomeAddress: [
          { required: true, message: "", trigger: "blur" },
          {
            min: 4,
            max: 40,
            message: "440",
            trigger: "blur",
          },
        ],
        // 
        stuPostCode: [
          { required: true, message: "", trigger: "blur" },
          {
            min: 6,
            max: 6,
            message: "6",
            trigger: "blur",
          },
        ],
        // 
        stuHealth: [
          { required: true, message: "", trigger: "blur" },
        ],
        // 
        stuEducationExperience: [
          { required: true, message: "", trigger: "blur" },
          {
            min: 10,
            message: "10",
            trigger: "blur",
          },
        ],
        // 
        stuName: [
          { required: true, message: "", trigger: "blur" },
          {
            min: 2,
            max: 8,
            message: "28",
            trigger: "blur",
          },
        ],
      },
    };
  },
  methods: {
    async addStudentInfo() {
      if (this.addInfo.stuGender === "man") {
        this.addInfo.stuGender = Number(0);
      } else if (this.addInfo.stuGender === "women") {
        this.addInfo.stuGender = Number(1);
      }
      if (this.addInfo.stuPoliticsStatus === "socialman") {
        this.addInfo.stuPoliticsStatus = Number(0);
      } else if (this.addInfo.stuPoliticsStatus === "leaguemember") {
        this.addInfo.stuPoliticsStatus = Number(1);
      } else if (this.addInfo.stuPoliticsStatus === "chinamember") {
        this.addInfo.stuPoliticsStatus = Number(2);
      }
      if (this.addInfo.stuHealth === "health") {
        this.addInfo.stuHealth = Number(0);
      } else if (this.addInfo.stuHealth === "better") {
        this.addInfo.stuHealth = Number(1);
      } else if (this.addInfo.stuHealth === "nothealth") {
        this.addInfo.stuHealth = Number(-1);
      }
      if (this.addInfo.stuHonor === "") {
        this.addInfo.stuHonor = Number(0);
      } else {
        this.addInfo.stuHonor = Number(1);
      }
      if (this.addInfo.stuPunishment === "") {
        this.addInfo.stuPunishment = Number(0);
      } else {
        this.addInfo.stuPunishment = Number(1);
      }
      // console.log(this.addInfo);
      this.$refs.addFormRules.validate(async (valid) => {
        if (!valid) return;
        const confirmResult = await this.$confirm(
          "?",
          "",
          {
            confirmButtonText: "",
            cancelButtonText: "",
            type: "warning",
          }
        ).catch((err) => err);
        if (confirmResult !== "confirm") {
          return this.$message.info("");
        }
        this.fullscreenLoading = true;
        const { data: res } = await this.$http.post(
          "/graduate-project/addStuInfo",
          this.addInfo
        );
        if (res.status !== 200) {
          this.$http.error("");
        }
        // console.log(res);
        this.fullscreenLoading = false;
        this.$message.success(res.message);
        this.addInfo = {};
      });
    },
    resetInfo(addFormRules) {
      this.fullscreenLoading = true;
      this.$refs[addFormRules].resetFields();
      this.fullscreenLoading = false;
    },
  },
};
</script>

<style lang="less" scoped>
</style>
